/**
 * KeywordHelp Module - Provides keyword help functionality for NextBuild
 */
import * as vscode from 'vscode';
import * as fs from 'fs';
import * as path from 'path';
import * as markdownHelper from './utils/markdownHelper';

// Store keyword help data
export let keywordHelp: Record<string, any> = {};

// A simple history tracker for keyword navigation
// This is a more straightforward approach than using webview state
const keywordHistory: Map<string, string[]> = new Map();

/**
 * Loads keyword help data from keywords.json
 */
export async function loadKeywordHelp(context: vscode.ExtensionContext): Promise<boolean> {
    try {
        const keywordPath = path.join(context.extensionPath, "data", "keywords.json");
        // Check if the file exists
        if (!fs.existsSync(keywordPath)) {
            console.error(`Keyword help file not found at: ${keywordPath}`);
            return false;
        }
        
        const fileContent = fs.readFileSync(keywordPath, "utf8");
        keywordHelp = JSON.parse(fileContent);
        
        // Store original case for display, but normalize keys for case-insensitive lookup
        const normalizedHelp: Record<string, any> = {};
        Object.keys(keywordHelp).forEach(key => {
            const upperKey = key.toUpperCase();
            if (typeof keywordHelp[key] === 'object' && keywordHelp[key] !== null) {
                // Entry is an object with metadata - preserve original key for display
                normalizedHelp[upperKey] = {
                    ...keywordHelp[key],
                    displayName: key // Store original case for display
                };
            } else {
                // Old format - just a string content
                normalizedHelp[upperKey] = {
                    content: keywordHelp[key],
                    isManualOnly: false,
                    displayName: key // Store original case for display
                };
            }
        });

        keywordHelp = normalizedHelp;
        return Object.keys(keywordHelp).length > 0;
    } catch (error) {
        console.error('Error loading keyword help:', error);
        return false;
    }
}

/**
 * Shows help for currently selected word
 */
export async function showHelp() {
    console.log('[NextBuild Viewers] showHelp command triggered.');
    const editor = vscode.window.activeTextEditor;
    if (!editor) {
        vscode.window.showInformationMessage("No active editor found");
        console.log('[NextBuild Viewers] No active editor.');
        return;
    }
    
    const languageId = editor.document.languageId;
    console.log(`[NextBuild Viewers] Current languageId: ${languageId}`);
    const isNextbuildFile = languageId === 'nextbuild';
    
    const position = editor.selection.active;
    const wordPattern = isNextbuildFile 
        ? /[A-Za-z0-9_\.]+/ 
        : /[A-Za-z0-9_\.]+/;
        
    const wordRange = editor.document.getWordRangeAtPosition(position, wordPattern);
    
    let word = "";

    if (!wordRange) {
        console.log('[NextBuild Viewers] No wordRange found. Opening Index.');
        word = "INDEX";
    } else {
        word = editor.document.getText(wordRange).toUpperCase();
        console.log(`[NextBuild Viewers] Detected word: ${word}`);
    }
    
    if (!word) {
        vscode.window.showInformationMessage("No keyword found at cursor position");
        console.log('[NextBuild Viewers] Empty word detected.');
        return;
    }
    
    await showKeywordHelp(word, isNextbuildFile);
}

/**
 * Shows help for a specific keyword
 */
export async function showKeywordHelp(keyword: string, isNextbuildFile: boolean = false) {
    console.log('[NextBuild Viewers] Keyword help object:', keywordHelp);
    
    const keywordUpper = keyword.toUpperCase();
    let helpEntry = keywordHelp[keywordUpper];
    let helpContent: string | undefined;
    
    // Extract the content from the help entry, handling both formats
    if (helpEntry) {
        helpContent = typeof helpEntry === 'object' ? helpEntry.content : helpEntry;
    }
    
    if (!helpContent && Object.keys(keywordHelp).length > 0) {
        console.log(`[NextBuild Viewers] No exact match for ${keyword}. Searching similar...`);
        const similarKeywords = Object.keys(keywordHelp).filter(
            key => key.includes(keywordUpper) || keywordUpper.includes(key)
        );
        
        if (similarKeywords.length > 0) {
            console.log(`[NextBuild Viewers] Similar keywords found: ${similarKeywords.join(', ')}`);
            const selected = await vscode.window.showQuickPick(similarKeywords, {
                placeHolder: `No exact match for "${keyword}". Select a similar keyword:`,
            });
            
            if (selected) {
                helpEntry = keywordHelp[selected];
                helpContent = typeof helpEntry === 'object' ? helpEntry.content : helpEntry;
                keyword = selected; // Update the keyword for the panel title
                console.log(`[NextBuild Viewers] User selected similar keyword: ${selected}`);
            }
        }
    }
    
    if (helpContent) {
        console.log(`[NextBuild Viewers] Displaying help for ${keyword}`);
        
        // Create a new panel - we'll always create a new one
        // since we can't reliably reuse existing panels
        // Get display name for the keyword
        const keywordEntry = keywordHelp[keywordUpper];
        const displayKeyword = (keywordEntry && keywordEntry.displayName) ? keywordEntry.displayName : keyword;

        const panel = vscode.window.createWebviewPanel(
            'nextbuildKeywordHelp',
            `NextBuild Help: ${displayKeyword}`,
            vscode.ViewColumn.Beside,
            {
                enableScripts: true, // Enable scripts to handle inter-keyword navigation
                localResourceRoots: [],
                retainContextWhenHidden: true
            }
        );
        
        // Generate a unique ID for this panel to track its history
        const panelId = `panel_${Date.now()}`;
        keywordHistory.set(panelId, [keyword]);
        
        // Handle messages from the webview
        panel.webview.onDidReceiveMessage(
            async (message) => {
                if (message.command === 'showKeywordHelp' && message.keyword) {
                    console.log(`[NextBuild Viewers] Received request to show help for: ${message.keyword}`);
                    
                    // Update the history for this panel
                    const history = keywordHistory.get(panelId) || [];
                    if (history[history.length - 1] !== message.keyword) {
                        history.push(message.keyword);
                        keywordHistory.set(panelId, history.slice(-10)); // Keep only last 10
                    }
                    
                    // Update the panel title
                    const keywordEntry = keywordHelp[message.keyword.toUpperCase()];
                    const displayName = (keywordEntry && keywordEntry.displayName) ? keywordEntry.displayName : message.keyword;
                    panel.title = `NextBuild Help: ${displayName}`;
                    // Show the new keyword help in the same panel
                    await updatePanelContent(panel, message.keyword, isNextbuildFile, panelId);
                    // Send contentUpdated message
                    panel.webview.postMessage({ command: 'contentUpdated' });
                } else if (message.command === 'navigateBack' && message.panelId) {
                    // Handle back navigation
                    const history = keywordHistory.get(message.panelId) || [];
                    if (history.length > 1) {
                        // Remove current keyword and go to previous
                        history.pop();
                        const prevKeyword = history[history.length - 1];
                        const keywordEntry = keywordHelp[prevKeyword.toUpperCase()];
                        const displayName = (keywordEntry && keywordEntry.displayName) ? keywordEntry.displayName : prevKeyword;
                        panel.title = `NextBuild Help: ${displayName}`;
                        await updatePanelContent(panel, prevKeyword, isNextbuildFile, message.panelId);
                        // Send contentUpdated message
                        panel.webview.postMessage({ command: 'contentUpdated' });
                    }
                } else if (message.command === 'openExternalLink' && message.url) {
                    vscode.env.openExternal(vscode.Uri.parse(message.url));
                } else if (message.command === 'openMdFile' && message.file) {
                    // Open the .md file in the editor (relative to docs/)
                    const filePath = path.join(__dirname, '../../docs', message.file);
                    if (fs.existsSync(filePath)) {
                        const uri = vscode.Uri.file(filePath);
                        vscode.commands.executeCommand('vscode.open', uri);
                    } else {
                        vscode.window.showWarningMessage(`File not found: ${filePath}`);
                    }
                } else if (message.command === 'openEditor' && message.editorType) {
                    // Handle requests to open specific editors
                    console.log(`[NextBuild Viewers] Opening editor: ${message.editorType}`);
                    
                    // Open the requested editor
                    switch (message.editorType) {
                        case 'sprite':
                            vscode.commands.executeCommand('nextbuild-viewers.openSpriteViewer');
                            break;
                        case 'map':
                            vscode.commands.executeCommand('nextbuild-viewers.openMapViewer');
                            break;
                        case 'palette':
                            vscode.commands.executeCommand('nextbuild-viewers.openPaletteViewer');
                            break;
                        case 'layer2':
                            vscode.commands.executeCommand('nextbuild-viewers.openLayer2Viewer');
                            break;
                        default:
                            vscode.window.showWarningMessage(`Unknown editor type: ${message.editorType}`);
                    }
                } else if (message.command === 'openExampleFile' && message.filePath) {
                    // Handle requests to open/create example files
                    console.log(`[NextBuild Viewers] Handling example file link: ${message.filePath}`);
                    
                    const workspaceFolder = vscode.workspace.workspaceFolders?.[0];
                    if (!workspaceFolder) {
                        vscode.window.showErrorMessage('No workspace folder open to find or create the file in.');
                        return;
                    }
                    
                    // 1. Check if the file already exists in the workspace.
                    // We remove any leading slash from the link to correctly join with the workspace path.
                    const workspaceFilePath = path.join(workspaceFolder.uri.fsPath, message.filePath.startsWith('/') ? message.filePath.substring(1) : message.filePath);
                    
                    if (fs.existsSync(workspaceFilePath)) {
                        console.log(`[NextBuild Viewers] Found existing file in workspace, opening: ${workspaceFilePath}`);
                        const uri = vscode.Uri.file(workspaceFilePath);
                        vscode.commands.executeCommand('vscode.open', uri);
                        return; // Done.
                    }
                    
                    // 2. If not found, fall back to creating it from an extension template.
                    console.log(`[NextBuild Viewers] File not in workspace. Looking for template.`);
                    const exampleFileName = path.basename(message.filePath);
                    const exampleTemplatePath = path.join(__dirname, '../docs/nextbuild/markdown/examples/', exampleFileName);
                    
                    if (fs.existsSync(exampleTemplatePath)) {
                        try {
                            const exampleContent = fs.readFileSync(exampleTemplatePath, 'utf8');
                            const action = await vscode.window.showInformationMessage(
                                `The file '${exampleFileName}' was not found in your workspace.`,
                                { modal: true, detail: `Would you like to create it using the provided example?` },
                                'Create File'
                            );
                            
                            if (action === 'Create File') {
                                fs.writeFileSync(workspaceFilePath, exampleContent, 'utf8');
                                const uri = vscode.Uri.file(workspaceFilePath);
                                vscode.commands.executeCommand('vscode.open', uri);
                                vscode.window.showInformationMessage(`Created example file: ${exampleFileName}`);
                            }
                        } catch (error) {
                            console.error('Error creating example file from template:', error);
                            vscode.window.showErrorMessage(`Failed to create example file: ${error}`);
                        }
                    } else {
                        vscode.window.showWarningMessage(`'${exampleFileName}' was not found in your workspace, and no corresponding example template exists in the extension.`);
                    }
                } else if (message.command === 'scrollToTop') {
                    setTimeout(() => {
                        window.scrollTo(0, 0);
                    }, 50);
                }
            },
            undefined,
            []
        );
        
        // Clean up history when panel is disposed
        panel.onDidDispose(() => {
            keywordHistory.delete(panelId);
        });
        
        // Update the panel content
        await updatePanelContent(panel, keyword, isNextbuildFile, panelId);
        
        // Send a message to notify that content is updated
        panel.webview.postMessage({ command: 'contentUpdated' });
    } else {
        vscode.window.showInformationMessage(`No help found for keyword "${keyword}".`);
        console.log(`[NextBuild Viewers] No help content found for ${keyword}`);
    }
}

/**
 * Updates webview panel content with help for a specific keyword
 */
async function updatePanelContent(panel: vscode.WebviewPanel, keyword: string, isNextbuildFile: boolean, panelId: string) {
    const keywordUpper = keyword.toUpperCase();
    const helpEntry = keywordHelp[keywordUpper];
    
    if (!helpEntry) {
        panel.webview.html = `<html><body><h1>Error</h1><p>No help content found for "${keyword}"</p></body></html>`;
        return;
    }
    
    // Extract content based on the entry type
    const helpContent = typeof helpEntry === 'object' ? helpEntry.content : helpEntry;
    const isManualEntry = typeof helpEntry === 'object' && helpEntry.isManualOnly === true;
    const entryType = isManualEntry ? 'Manual' : 'Keyword';
    const displayKeyword = keyword.startsWith('_') ? keyword.substring(1) : keyword;
    // Create markdown content
    const markdownContent = `## NextBuild Help: ${displayKeyword}
${helpContent}`;
//     ${isManualEntry ? '*Documentation entry*' : ''}
// ${isNextbuildFile ? '*Context: NextBuild Studio language*' : ''}

    // Convert markdown to HTML using markdown-it with highlight.js
    const baseDir = path.join(__dirname, '../../docs'); // Adjust as needed
    const processedMarkdown = markdownHelper.processMarkdownLinksForWebview(markdownContent, keywordHelp, baseDir);
    const html = markdownHelper.markdownToHtml(processedMarkdown);
    
    // Process HTML to add IDs to headings
    const htmlWithIds = addIdsToHeadings(html);
    
    // Get highlight.js CDN script
    const highlightJsCDN = `
        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/highlight.js/11.9.0/styles/base16/harmonic16-dark.min.css">
        <script src="https://cdnjs.cloudflare.com/ajax/libs/highlight.js/11.9.0/highlight.min.js"></script>
        <script>
            hljs.configure({
                languages: ['basic', 'wren', 'markdown'],
                classPrefix: 'hljs-'
            });
            hljs.registerLanguage('basic', function(hljs) {
                return {
                    case_insensitive: true,
                    keywords: {
                        keyword: 'sub function dim as byval return end if then else for next do while loop elseif to print #include asm',
                        type: 'ubyte uinteger string',
                        built_in: 'InitSprites InitPalette GetReg GetMMU FreeBank FlipBuffer NextReg UpdateSprite LoadSD LoadSDBank WaitKey SpritesOn InitSprites2',
                        operator: '[-+*/%<>=!&|]',
                        variable: '[@$]',
                        z80: '[A-Za-z0-9_]+:'
                    },         
                    literal: {
                        className: 'hljs-number',
                        begin: '\\b[0-9]+\\b|\\$[0-9A-F]+\\b|\\%[01]+\\b',
                        relevance: 0
                    },
                    string: {
                        className: 'hljs-string',
                        begin: '"', 
                        end: '"',
                        relevance: 0
                    },
                    operator: {
                        className: 'hljs-operator',
                        begin: '[-+*/%<>=!&|]',
                        relevance: 0
                    },
                    variable: {
                        className: 'hljs-variable',
                        begin: '[@$]',
                        relevance: 0
                    },
                    contains: [
                        hljs.QUOTE_STRING_MODE,      // Handle quoted strings
                        hljs.COMMENT("'", '$'),      // Single line comments
                        hljs.COMMENT('rem', '$', {relevance: 10})  // REM comments
                    ]
                };
            });
            document.addEventListener('DOMContentLoaded', (event) => {
                hljs.highlightAll();
                // Add table borders
                document.querySelectorAll('table').forEach(table => {
                    table.style.borderCollapse = 'collapse';
                    table.style.border = '1px solid #bb2';
                    table.querySelectorAll('th,td').forEach(cell => {
                        cell.style.border = '1px solid #bb1';
                        cell.style.padding = '8px';
                    });
                });
            });
        </script>
    `;
    
    // Generate breadcrumbs HTML
    let breadcrumbsHtml = '';
    const history = keywordHistory.get(panelId) || [keyword];

    if (history.length > 1) {
        breadcrumbsHtml = `
            <div class="breadcrumbs">
                ${history.map((k: string, index: number) => {
                    const entry = keywordHelp[k];
                    const displayName = (entry && entry.displayName) ? entry.displayName : k;
                    return index < history.length - 1
                        ? `<span class="breadcrumb-item" data-keyword="${k}">${displayName}</span><span class="breadcrumb-separator"></span>`
                        : `<span class="current-keyword">${displayName}</span>`;
                }).join('')}
            </div>
        `;
    }
    
    // Add a back button if there's history
    let backButton = '';
    if (history.length > 1) {
        backButton = `<button id="back-button" class="navigation-button"> Back</button>`;
    }
    
    // Generate navigation list for normal keywords (not manual-only)
    const normalKeywordsHtml = Object.keys(keywordHelp)
        .filter(key => {
            const entry = keywordHelp[key];
            // Show keywords that are not manual-only AND should be shown in keyword list
            return typeof entry === 'object' &&
                  entry.isManualOnly !== true &&
                  entry.showInKeywordList !== false;
        })
        .map(key => {
            const entry = keywordHelp[key];
            const displayName = entry.displayName || key;
            return `<span class="keyword-chip" data-keyword="${key}">${displayName}</span>`;
        })
        .join('');
    
    // Generate navigation list for underscore-prefixed entries (special references)
    const specialReferencesHtml = Object.keys(keywordHelp)
        .filter(key => key.startsWith('_'))
        .map(key => {
            const entry = keywordHelp[key];
            const displayName = entry.displayName ? entry.displayName.substring(1) : key.substring(1);
            return `<span class="keyword-chip special-reference" data-keyword="${key}" >${displayName}</span>`;
        })
        .join('');
    
    // Set the HTML content
    panel.webview.html = `
        <!DOCTYPE html>
        <html lang="en">
        <head>
            <meta charset="UTF-8">
            <meta name="viewport" content="width=device-width">
            
            ${highlightJsCDN}
            <style>
                ${markdownHelper.getMarkdownCss()}
                /* Custom heading styles */
                h1 {
                    color:hsl(55, 95.10%, 84.10%);
                    font-size: 2em;
                    border-bottom: 2px solid rgb(62, 195, 66);
                    padding-bottom: 0.2em;
                }
                h2 {
                    color:rgb(29, 153, 247);
                    font-size: 1.5em;
                    margin-top: 1.2em;
                }
                h3 {
                    color:rgb(10, 141, 14);
                    font-size: 1.2em;
                    margin-top: 1.0em;
                }

                .navigation-button {
                    background-color: var(--vscode-button-background);
                    color: var(--vscode-button-foreground);
                    border: none;
                    padding: 6px 12px;
                    border-radius: 3px;
                    cursor: pointer;
                    margin-bottom: 15px;
                }
                .navigation-button:hover {
                    background-color: var(--vscode-button-hoverBackground);
                }

                .hljs-keyword {
                    color:rgb(192, 205, 15);
                    font-weight: bold;
                }
                .hljs-built_in {
                    color:rgb(205, 192, 15);
                }
                .hljs-operator {
                    color:rgb(205, 53, 15);
                }
                .hljs-variable {
                    color:rgb(241, 121, 61);
                }
                .hljs-number {
                    color:rgb(132, 199, 50);
                }
                .hljs-title {
                    color:rgb(132, 199, 50);
                }
                .hljs-z80 {
                    color:rgb(132, 199, 50);
                }
                .hljs-string {
                    color:rgb(241, 121, 61);
                }
                .hljs-comment {
                    color:rgb(25, 188, 36);
                }

                /* Style for special reference entries */
                .special-reference {
                    background-color:var(--vscode-focusBorder);
                    border: 1px solid var(--vscode-);
                    border: 1px solid black;
                    color: var(--vscode-textPreformat-foreground);
                }
                
                /* Style for example file links */
                .example-file-link {
                    color: var(--vscode-textLink-foreground);
                    text-decoration: none;
                    border-bottom: 1px dotted var(--vscode-textLink-foreground);
                    cursor: pointer;
                }
                .example-file-link:hover {
                    color: var(--vscode-textLink-activeForeground);
                    border-bottom: 1px solid var(--vscode-textLink-activeForeground);
                }
                .example-file-link::before {
                    content: " ";
                    opacity: 0.7;
                }
                .example-file-link::after {
                    content: " (create)";
                    font-size: 0.8em;
                    opacity: 0.6;
                    font-style: italic;
                }
            </style>
        </head>
        <body>
            <div id="top"></div>
            <div class="entry-type"></div>
            ${backButton}
            ${breadcrumbsHtml}
            ${htmlWithIds}
            
            <div class="navigation">
                <h3>All Available Keywords</h3>
                <div class="keywords-list">
                    ${normalKeywordsHtml}
                </div>
                
                <h3>Quick References</h3>
                <div class="special-references" style="display: flex; flex-wrap: wrap; flex-direction: row;">
                    ${specialReferencesHtml}
                </div>
            </div>
            
            <script>
                // Use VS Code API for sending messages
                const vscode = acquireVsCodeApi();
                const panelId = "${panelId}";
                
                // Function to navigate to another keyword
                function showKeyword(keyword) {
                    vscode.postMessage({
                        command: 'showKeywordHelp',
                        keyword: keyword,
                        panelId: panelId
                    });
                }
                // Function to navigate back
                function navigateBack() {
                    vscode.postMessage({
                        command: 'navigateBack',
                        panelId: panelId
                    });
                }
                // Add navigate back with mouse or keyboard
                document.addEventListener('keydown', (event) => {
                    if (event.key === 'Backspace') {
                        navigateBack();
                    }
                });
                // Add navigate back with side mouse button
                document.addEventListener('auxclick', (event) => {
                    if (event.button === 3) { // Side mouse button (usually back)
                        navigateBack();
                    }
                });

                // Add click handler to back button
                const backButton = document.getElementById('back-button');
                if (backButton) {
                    backButton.addEventListener('click', navigateBack);
                }
                // Add click handlers to all keyword links
                document.querySelectorAll('.keyword-link').forEach(link => {
                    link.addEventListener('click', (event) => {
                        event.preventDefault();
                        const keyword = link.getAttribute('data-keyword');
                        if (keyword) {
                            showKeyword(keyword);
                        }
                    });
                });
                // Add click handlers to all .md-file-link links
                document.querySelectorAll('.md-file-link').forEach(link => {
                    link.addEventListener('click', (event) => {
                        event.preventDefault();
                        const mdfile = link.getAttribute('data-mdfile');
                        if (mdfile) {
                            vscode.postMessage({
                                command: 'openMdFile',
                                file: mdfile
                            });
                        }
                    });
                });
                // Add click handlers to all keyword chips
                document.querySelectorAll('.keyword-chip').forEach(chip => {
                    chip.addEventListener('click', (event) => {
                        const keyword = chip.getAttribute('data-keyword');
                        if (keyword) {
                            showKeyword(keyword);
                        }
                    });
                });
                // Add click handlers to breadcrumb items
                document.querySelectorAll('.breadcrumb-item').forEach(item => {
                    item.addEventListener('click', (event) => {
                        const keyword = item.getAttribute('data-keyword');
                        if (keyword) {
                            showKeyword(keyword);
                        }
                    });
                });
                // Handle external links
                document.querySelectorAll('.external-link').forEach(link => {
                    link.addEventListener('click', (event) => {
                        event.preventDefault();
                        vscode.postMessage({
                            command: 'openExternalLink',
                            url: link.getAttribute('href')
                        });
                    });
                });
                // Handle in-page anchor links (e.g., [Go to top](#top))
                // But exclude our custom link types
                document.querySelectorAll('a[href^="#"]:not(.example-file-link):not(.keyword-link):not(.md-file-link)').forEach(link => {
                    link.addEventListener('click', (event) => {
                        const href = link.getAttribute('href');
                        if (href && href.length > 1) {
                            const targetId = href.substring(1);
                            const target = document.getElementById(targetId);
                            if (target) {
                                event.preventDefault();
                                target.scrollIntoView({ behavior: 'smooth' });
                            }
                        }
                    });
                });
                
                // Add ability to open editor views
                document.querySelectorAll('.open-editor').forEach(link => {
                    link.addEventListener('click', (event) => {
                        event.preventDefault();
                        const editorType = link.getAttribute('data-editor');
                        if (editorType) {
                            vscode.postMessage({
                                command: 'openEditor',
                                editorType: editorType
                            });
                        }
                    });
                });
                
                // Add ability to open example files
                document.querySelectorAll('.example-file-link').forEach(link => {
                    link.addEventListener('click', (event) => {
                        event.preventDefault();
                        event.stopPropagation();
                        const filePath = link.getAttribute('data-filepath');
                        if (filePath) {
                            vscode.postMessage({
                                command: 'openExampleFile',
                                filePath: filePath
                            });
                        }
                    });
                });

                // Scroll to top immediately when content is loaded
                window.scrollTo(0, 0);

                // Listen for messages from VS Code
                window.addEventListener('message', event => {
                    const message = event.data;
                    if (message.command === 'contentUpdated') {
                        // Additional scroll to top after content update
                        window.scrollTo(0, 0);
                    }
                });
            </script>
        </body>
        </html>
    `;
}

/**
 * Process HTML to add IDs to headings so anchor links work
 */
function addIdsToHeadings(html: string): string {
    // Regular expression to find heading tags (h1-h6)
    const headingRegex = /<h([1-6])>(.*?)<\/h\1>/g;
    
    return html.replace(headingRegex, (match, level, content) => {
        // Create an ID from the content - lowercase, replace spaces with hyphens
        const rawId = content.replace(/<.*?>/g, '').trim();
        const id = rawId
            .toLowerCase()
            .replace(/[^\w\s-]/g, '') // Remove special chars
            .replace(/\s+/g, '-'); // Replace spaces with hyphens
        
        // Return the heading with an ID attribute
        return `<h${level} id="${id}">${content}</h${level}>`;
    });
}

/**
 * Edit keywords file
 */
export async function editKeywordsFile(context: vscode.ExtensionContext) {
    try {
        const keywordPath = path.join(context.extensionPath, "data", "keywords.json");
        
        // Check if the directory exists, if not create it
        const dataDir = path.join(context.extensionPath, "data");
        if (!fs.existsSync(dataDir)) {
            fs.mkdirSync(dataDir, { recursive: true });
        }
        
        // Check if the file exists, if not create a template
        if (!fs.existsSync(keywordPath)) {
            const template = {
                "IF": {
                    "content": "```z80\nIF  THEN  [ELSE ] END IF\n```\nBranches based on condition.",
                    "isManualOnly": false
                },
                "FOR": {
                    "content": "```z80\nFOR var = start TO end [STEP s]  NEXT\n```\nCreates a loop from start to end.",
                    "isManualOnly": false
                },
                "REG_54": {
                    "content": "Register 54 - MMU slot 2 control\n\nControls the 8K memory page at 0x4000-0x5FFF.",
                    "showInKeywordList": false,
                    "isManualOnly": false
                },
                "SPRITES": {
                    "content": "# Sprite System\n\n## Overview\nThis manual page describes how to use sprites in NextBuild.\n\n## Formats\nSprites can be defined in various formats...",
                    "isManualOnly": true
                },
                "EXAMPLE_KEYWORD": "For backwards compatibility, you can still use simple strings for content.",
                "FORMAT_NOTE": {
                    "content": "For more control, use object format with 'content' and 'isManualOnly' properties.\nSet isManualOnly to true for manual-only pages that won't show in hover help.\nSet showInKeywordList to false for registers/ports that should appear in hover but not in the keywords list.",
                    "isManualOnly": true
                }
            };
            
            fs.writeFileSync(keywordPath, JSON.stringify(template, null, 2), "utf8");
        }
        
        // Open the file in the editor
        const uri = vscode.Uri.file(keywordPath);
        await vscode.commands.executeCommand('vscode.open', uri);
        
        // Show info message
        vscode.window.showInformationMessage(
            'Edit keywords.json file. For standard keywords, use "KEYWORD": { "content": "markdown content" }. ' +
            'For manual-only entries, add "isManualOnly": true to prevent them from appearing in hover help.'
        );
        
        return true;
    } catch (error) {
        console.error('Error editing keywords file:', error);
        vscode.window.showErrorMessage(`Error opening keywords file: ${error}`);
        return false;
    }
}

/**
 * Hover Provider for NextBuild Keywords
 */
export class NextBuildHoverProvider implements vscode.HoverProvider {
    // Add private field for constants
    private nextbuildConstants: Record<string, any> = {};

    // Add setter method for constants
    public setConstants(constants: Record<string, any>) {
        this.nextbuildConstants = constants;
    }

    public provideHover(
        document: vscode.TextDocument,
        position: vscode.Position,
        token: vscode.CancellationToken
    ): vscode.ProviderResult<vscode.Hover> {
        const config = vscode.workspace.getConfiguration('nextbuild-viewers');
        if (!config.get('hoverHelp.enable', true)) {
            return undefined;
        }

        const wordRange = document.getWordRangeAtPosition(position, /[A-Za-z0-9_\.]+/);
        if (!wordRange) {
            return undefined;
        }

        const word = document.getText(wordRange).toUpperCase();
        
        // First check if it's a constant
        if (this.nextbuildConstants[word]) {
            const constantEntry = this.nextbuildConstants[word];
            const content = `**${word}** = ${constantEntry.value}\n\n${constantEntry.description}`;
            const mdString = new vscode.MarkdownString(content);
            mdString.isTrusted = true;
            mdString.supportHtml = true;
            return new vscode.Hover(mdString);
        }
        
        // Then check if it's a keyword
        if (!keywordHelp[word] || 
            (typeof keywordHelp[word] === 'object' && keywordHelp[word].isManualOnly === true)) {
            return undefined;
        }
        
        // Extract content from either object format or direct string
        let content = typeof keywordHelp[word] === 'object' ? keywordHelp[word].content : keywordHelp[word];

        // Process the markdown to make links work properly in hover
        const processedContent = markdownHelper.processMarkdownLinksForHover(content, keywordHelp);
        const mdString = new vscode.MarkdownString(processedContent);
        mdString.isTrusted = true; // Enable command URIs
        mdString.supportHtml = true; // Support HTML
        
        return new vscode.Hover(mdString);
    }
}

/**
 * Completion Item Provider for NextBuild Keywords and Constants
 */
export class NextBuildCompletionItemProvider implements vscode.CompletionItemProvider {
    // Constants data - this will need to be initialized or imported separately
    private nextbuildConstants: Record<string, any> = {};
    
    // Set constants data
    public setConstants(constants: Record<string, any>) {
        this.nextbuildConstants = constants;
    }
    
    public provideCompletionItems(
        document: vscode.TextDocument,
        position: vscode.Position,
        token: vscode.CancellationToken,
        context: vscode.CompletionContext
    ): vscode.ProviderResult<vscode.CompletionItem[] | vscode.CompletionList> {
        const completions: vscode.CompletionItem[] = [];
        const config = vscode.workspace.getConfiguration('nextbuild-viewers');

        // Add keywords from keywordHelp if enabled
        if (config.get('completion.enableKeywords', true)) {
            for (const keyword in keywordHelp) {
                if (Object.prototype.hasOwnProperty.call(keywordHelp, keyword)) {
                    const helpText = keywordHelp[keyword];
                    
                    // Skip manual-only items from completion
                    if (typeof helpText === 'object' && helpText.isManualOnly === true) {
                        continue;
                    }
                    
                    const displayName = (typeof helpText === 'object' && helpText.displayName) ? helpText.displayName : keyword;
                    const item = new vscode.CompletionItem(displayName, vscode.CompletionItemKind.Keyword);
                    item.insertText = displayName.toLowerCase(); // Insert lowercase version
                    if (helpText) {
                        item.documentation = new vscode.MarkdownString(
                            typeof helpText === 'object' ? helpText.content : helpText
                        );
                    }
                    item.detail = "NextBuild Keyword";
                    completions.push(item);
                }
            }
        }

        // Add constants from nextbuildConstants if enabled
        if (config.get('completion.enableConstants', true)) {
            for (const constantName in this.nextbuildConstants) {
                if (Object.prototype.hasOwnProperty.call(this.nextbuildConstants, constantName)) {
                    const constantEntry = this.nextbuildConstants[constantName];
                    const item = new vscode.CompletionItem(constantName, vscode.CompletionItemKind.Constant);
                    
                    let detail = "NextBuild Constant";
                    if (constantEntry.value !== undefined) {
                        detail += ` = ${constantEntry.value}`;
                    }
                    item.detail = detail;
                    
                    item.documentation = new vscode.MarkdownString(constantEntry.description);
                    completions.push(item);
                }
            }
        }
        return completions;
    }
}
export async function searchKeywordHelp() {
    console.log('[NextBuild Viewers] Search keyword help triggered.');
    
    if (Object.keys(keywordHelp).length === 0) {
        vscode.window.showInformationMessage('No keyword help data loaded.');
        return;
    }
    
    const allKeywords = Object.keys(keywordHelp).map(key => {
        const entry = keywordHelp[key];
        return {
            label: (entry && entry.displayName) ? entry.displayName : key,
            detail: key, // Show the uppercase key as detail for reference
            key: key // Store the uppercase key for lookup
        };
    }).sort((a, b) => a.label.localeCompare(b.label));

    const selected = await vscode.window.showQuickPick(allKeywords, {
        placeHolder: 'Search and select a keyword for help...',
        matchOnDescription: true,
        matchOnDetail: true
    });
    
    if (selected) {
        await showKeywordHelp(selected.key, true);
    }
}

