import * as vscode from 'vscode';
import { SpriteFileData } from './spriteDataHandler';
import { encodeSpriteData } from './spriteDataHandler';
import { PaletteColor } from './paletteUtils';

// Define structure for sprite document state
export interface SpriteDocumentState {
    uri: vscode.Uri;
    initialFileData: Uint8Array;    // Original file bytes for revert
    currentSpriteData: SpriteFileData | null; // Parsed data
    customPaletteHex: PaletteColor[] | null;
    customPaletteName: string;
    isDirty: boolean;
    // Add other relevant state if needed (e.g., viewState snapshot for backup?)
}

// Wrapper class implementing CustomDocument for Sprites
export class SpriteDocument implements vscode.CustomDocument {
    private _state: SpriteDocumentState;

    constructor(state: SpriteDocumentState) {
        this._state = state;
    }

    public get uri(): vscode.Uri { return this._state.uri; }
    public get state(): SpriteDocumentState { return this._state; }

    // Helper to get the bytes to be saved/backed up
    public get documentData(): Uint8Array {
        if (this._state.currentSpriteData) {
            try {
                // Re-encode the current sprite data state
                return encodeSpriteData(this._state.currentSpriteData);
            } catch (e) {
                console.error("Error encoding sprite data for save:", e);
                // Fallback to initial data on encoding error?
                vscode.window.showErrorMessage("Failed to encode sprite data for saving. Reverting to original data might occur.");
                return this._state.initialFileData;
            }
        }
        // If no parsed data, return initial bytes
        return this._state.initialFileData;
    }

    dispose(): void {
        console.log("Disposing sprite document:", this.uri.toString());
        // Add any specific sprite document cleanup if needed
    }
} 