import * as vscode from 'vscode';
import * as fs from 'fs-extra';
import * as path from 'path';
import * as https from 'https';
import * as http from 'http';

interface VersionInfo {
    version: string;
    changelog: string;
    download: string;
}

interface ComponentInfo {
    version: string;
    url: string;
    localPath: string;
    description?: string;
}

interface ComponentManifest {
    version: string;
    components: Record<string, ComponentInfo>;
}

interface RootComponentInfo {
    version: string;
    url: string;
    targetFolder: string;
    description?: string;
}

interface RootComponentManifest {
    version: string;
    components: Record<string, RootComponentInfo>;
}

export class UpdateManager {
    private context: vscode.ExtensionContext;
    private updateServerUrl: string;
    private componentServerUrl: string;
    private rootComponentServerUrl: string;
    private currentVersionKey = 'nextbuild.currentVersion';
    private componentVersionKey = 'nextbuild.componentVersions';
    private rootComponentVersionKey = 'nextbuild.rootComponentVersions';

    constructor(context: vscode.ExtensionContext) {
        this.context = context;
        const config = vscode.workspace.getConfiguration('nextbuild-viewers');
        this.updateServerUrl = config.get('updateServer', 'https://zxnext.uk/nextbuild/version.json');
        this.componentServerUrl = config.get('componentUpdateServer', 'https://zxnext.uk/nextbuild/components.json');
        this.rootComponentServerUrl = config.get('rootUpdateServer', 'https://zxnext.uk/nextbuild/root-components.json');
    }

    async checkForUpdates(): Promise<boolean> {
        const config = vscode.workspace.getConfiguration('nextbuild-viewers');
        const checkForUpdates = config.get('checkForUpdates', true);
        if (!checkForUpdates) {
            return false;
        }

        try {
            const versionInfo = await this.fetchVersionInfo();
            const currentVersion = this.getCurrentVersion();
            
            return this.isNewerVersion(versionInfo.version, currentVersion);
        } catch (error) {
            console.error('Failed to check for updates:', error);
            return false;
        }
    }

    async checkForComponentUpdates(): Promise<boolean> {
        const config = vscode.workspace.getConfiguration('nextbuild-viewers');
        const checkForComponentUpdates = config.get('checkForComponentUpdates', true);
        if (!checkForComponentUpdates) {
            return false;
        }

        try {
            const manifest = await this.fetchComponentManifest();
            const currentVersions = this.getComponentVersions();
            
            // Check if any component has a newer version
            for (const [componentName, componentInfo] of Object.entries(manifest.components)) {
                const currentVersion = currentVersions[componentName] || '0.0.0';
                if (this.isNewerVersion(componentInfo.version, currentVersion)) {
                    return true;
                }
            }
            
            return false;
        } catch (error) {
            console.error('Failed to check for component updates:', error);
            return false;
        }
    }

    async checkForRootUpdates(): Promise<boolean> {
        const config = vscode.workspace.getConfiguration('nextbuild-viewers');
        const checkForRootUpdates = config.get('checkForRootUpdates', true);
        if (!checkForRootUpdates) {
            return false;
        }

        try {
            const manifest = await this.fetchRootComponentManifest();
            const currentVersions = this.getRootComponentVersions();
            
            // Check if any root component has a newer version
            for (const [componentName, componentInfo] of Object.entries(manifest.components)) {
                const currentVersion = currentVersions[componentName] || '0.0.0';
                if (this.isNewerVersion(componentInfo.version, currentVersion)) {
                    return true;
                }
            }
            
            return false;
        } catch (error) {
            console.error('Failed to check for root component updates:', error);
            return false;
        }
    }

    async updateComponents(): Promise<void> {
        const checkForUpdates = vscode.workspace.getConfiguration('nextbuild-viewers').get('checkForUpdates', true);
        if (!checkForUpdates) {
            vscode.window.showInformationMessage('Update checking is disabled in settings.');
            return;
        }

        try {
            const versionInfo = await this.fetchVersionInfo();
            const currentVersion = this.getCurrentVersion();
            
            if (!this.isNewerVersion(versionInfo.version, currentVersion)) {
                vscode.window.showInformationMessage('You are already using the latest version!');
                return;
            }

            const choice = await vscode.window.showInformationMessage(
                `NextBuild Studio update available: v${versionInfo.version}`,
                'View Changelog',
                'Download Update',
                'Later'
            );

            if (choice === 'View Changelog') {
                // Open changelog in browser
                vscode.env.openExternal(vscode.Uri.parse(versionInfo.changelog));
                
                // Ask again after viewing changelog
                const downloadChoice = await vscode.window.showInformationMessage(
                    `Would you like to download NextBuild Studio v${versionInfo.version}?`,
                    'Download Now',
                    'Later'
                );
                
                if (downloadChoice === 'Download Now') {
                    vscode.env.openExternal(vscode.Uri.parse(versionInfo.download));
                    this.saveCurrentVersion(versionInfo.version);
                }
            } else if (choice === 'Download Update') {
                // Open download link in browser
                vscode.env.openExternal(vscode.Uri.parse(versionInfo.download));
                this.saveCurrentVersion(versionInfo.version);
                
                vscode.window.showInformationMessage(
                    'Download started. Please install the new version when the download completes.'
                );
            }
        } catch (error) {
            console.error('Failed to check for updates:', error);
            vscode.window.showErrorMessage(`Failed to check for updates: ${error}`);
        }
    }

    async updateExtensionComponents(): Promise<void> {
        const config = vscode.workspace.getConfiguration('nextbuild-viewers');
        const checkForComponentUpdates = config.get('checkForComponentUpdates', true);
        if (!checkForComponentUpdates) {
            vscode.window.showInformationMessage('Component update checking is disabled in settings.');
            return;
        }

        try {
            const manifest = await this.fetchComponentManifest();
            const currentVersions = this.getComponentVersions();
            const updatesAvailable: string[] = [];

            // Check which components need updates
            for (const [componentName, componentInfo] of Object.entries(manifest.components)) {
                const currentVersion = currentVersions[componentName] || '0.0.0';
                if (this.isNewerVersion(componentInfo.version, currentVersion)) {
                    const displayName = componentInfo.description || componentName;
                    let updateText = `${displayName} (${currentVersion}  ${componentInfo.version})`;
                    
                    // Highlight CSpect updates
                    if (componentName.toLowerCase().includes('cspect')) {
                        updateText = ` ${updateText}`;
                    }
                    
                    updatesAvailable.push(updateText);
                }
            }

            if (updatesAvailable.length === 0) {
                vscode.window.showInformationMessage('All extension components are up to date!');
                return;
            }

            const choice = await vscode.window.showInformationMessage(
                `Extension component updates available:\n${updatesAvailable.join('\n')}`,
                'Update Components',
                'Later'
            );

            if (choice === 'Update Components') {
                await vscode.window.withProgress({
                    location: vscode.ProgressLocation.Notification,
                    title: 'Updating Extension Components',
                    cancellable: false
                }, async (progress) => {
                    const newVersions = { ...currentVersions };
                    let updated = 0;

                    for (const [componentName, componentInfo] of Object.entries(manifest.components)) {
                        const currentVersion = currentVersions[componentName] || '0.0.0';
                        if (this.isNewerVersion(componentInfo.version, currentVersion)) {
                            const displayName = componentInfo.description || componentName;
                            progress.report({ 
                                message: `Updating ${displayName}...`,
                                increment: (updated / updatesAvailable.length) * 100 
                            });

                            await this.downloadAndInstallComponent(componentInfo);
                            newVersions[componentName] = componentInfo.version;
                            updated++;
                            
                            // Note: CSpect is typically a root component, but if it's ever an extension component,
                            // the readme would be in the extension path instead
                        }
                    }

                    this.saveComponentVersions(newVersions);
                    vscode.window.showInformationMessage(
                        `Successfully updated ${updated} extension components. Please reload VS Code to apply changes.`,
                        'Reload Now'
                    ).then(selection => {
                        if (selection === 'Reload Now') {
                            vscode.commands.executeCommand('workbench.action.reloadWindow');
                        }
                    });
                });
            }
        } catch (error) {
            console.error('Failed to update extension components:', error);
            vscode.window.showErrorMessage(`Failed to update extension components: ${error}`);
        }
    }

    async updateRootComponents(): Promise<void> {
        const config = vscode.workspace.getConfiguration('nextbuild-viewers');
        const checkForRootUpdates = config.get('checkForRootUpdates', true);
        if (!checkForRootUpdates) {
            vscode.window.showInformationMessage('Root component update checking is disabled in settings.');
            return;
        }

        // Get root folder path
        const rootFolder = vscode.workspace.getConfiguration('nextbuild-viewers.linting').get<string>('rootFolderForIncludes', '');
        if (!rootFolder || rootFolder.trim() === '') {
            vscode.window.showErrorMessage('Root folder for includes is not configured. Please set it in settings.');
            return;
        }

        try {
            const manifest = await this.fetchRootComponentManifest();
            const currentVersions = this.getRootComponentVersions();
            const updatesAvailable: string[] = [];

            // Check which root components need updates
            for (const [componentName, componentInfo] of Object.entries(manifest.components)) {
                const currentVersion = currentVersions[componentName] || '0.0.0';
                if (this.isNewerVersion(componentInfo.version, currentVersion)) {
                    const displayName = componentInfo.description || componentName;
                    let updateText = `${displayName} (${currentVersion}  ${componentInfo.version})`;
                    
                    // Highlight CSpect updates
                    if (componentName.toLowerCase().includes('cspect')) {
                        updateText = ` ${updateText}`;
                    }
                    
                    updatesAvailable.push(updateText);
                }
            }

            if (updatesAvailable.length === 0) {
                vscode.window.showInformationMessage('All root components are up to date!');
                return;
            }

            const choice = await vscode.window.showInformationMessage(
                `NextBuild root component updates available:\n${updatesAvailable.join('\n')}`,
                'Update Components',
                'Later'
            );

            if (choice === 'Update Components') {
                await vscode.window.withProgress({
                    location: vscode.ProgressLocation.Notification,
                    title: 'Updating NextBuild Root Components',
                    cancellable: false
                }, async (progress) => {
                    const newVersions = { ...currentVersions };
                    let updated = 0;

                    for (const [componentName, componentInfo] of Object.entries(manifest.components)) {
                        const currentVersion = currentVersions[componentName] || '0.0.0';
                        if (this.isNewerVersion(componentInfo.version, currentVersion)) {
                            const displayName = componentInfo.description || componentName;
                            progress.report({ 
                                message: `Updating ${displayName}...`,
                                increment: (updated / updatesAvailable.length) * 100 
                            });

                            // Handle CSpect updates with installer
                            if (componentName.toLowerCase().includes('cspect')) {
                                await this.launchCSpectInstaller(rootFolder);
                            } else {
                                await this.downloadAndInstallRootComponent(componentInfo, rootFolder);
                            }
                            
                            newVersions[componentName] = componentInfo.version;
                            updated++;
                        }
                    }

                    this.saveRootComponentVersions(newVersions);
                    vscode.window.showInformationMessage(`Successfully updated ${updated} root components.`);
                });
            }
        } catch (error) {
            console.error('Failed to update root components:', error);
            vscode.window.showErrorMessage(`Failed to update root components: ${error}`);
        }
    }

    private async downloadAndInstallComponent(componentInfo: ComponentInfo): Promise<void> {
        const response = await fetch(componentInfo.url);
        if (!response.ok) {
            throw new Error(`Failed to download component: ${response.statusText}`);
        }

        const data = await response.arrayBuffer();
        const targetPath = path.join(this.context.extensionPath, componentInfo.localPath);
        
        // Ensure directory exists
        await fs.ensureDir(path.dirname(targetPath));
        
        // Write the file
        await fs.writeFile(targetPath, Buffer.from(data));
    }

    private async downloadAndInstallRootComponent(componentInfo: RootComponentInfo, rootFolder: string): Promise<void> {
        const response = await fetch(componentInfo.url);
        if (!response.ok) {
            throw new Error(`Failed to download root component: ${response.statusText}`);
        }

        const data = await response.arrayBuffer();
        const targetPath = path.join(rootFolder, componentInfo.targetFolder);
        
        // For zip files, we'll need to extract them
        if (componentInfo.url.endsWith('.zip')) {
            // Create a temporary file
            const tempPath = path.join(require('os').tmpdir(), `nextbuild-update-${Date.now()}.zip`);
            await fs.writeFile(tempPath, Buffer.from(data));
            
            try {
                // Simple zip extraction using built-in tools
                await this.extractZipFile(tempPath, targetPath);
            } finally {
                // Clean up temp file
                await fs.remove(tempPath);
            }
        } else {
            // Direct file copy
            await fs.ensureDir(path.dirname(targetPath));
            await fs.writeFile(targetPath, Buffer.from(data));
        }
    }

    private async extractZipFile(zipPath: string, extractTo: string): Promise<void> {
        // Ensure target directory exists
        await fs.ensureDir(extractTo);
        
        // Use a simple command-line extraction method for now
        // This will be replaced with proper zip library later
        const { spawn } = require('child_process');
        
        return new Promise((resolve, reject) => {
            let command: string;
            let args: string[];
            
            if (process.platform === 'win32') {
                // Use PowerShell on Windows
                command = 'powershell';
                args = ['-Command', `Expand-Archive -Path "${zipPath}" -DestinationPath "${extractTo}" -Force`];
            } else {
                // Use unzip on Unix-like systems
                command = 'unzip';
                args = ['-o', zipPath, '-d', extractTo];
            }
            
            const proc = spawn(command, args);
            
            proc.on('close', (code: number) => {
                if (code === 0) {
                    resolve();
                } else {
                    reject(new Error(`Extraction failed with code ${code}`));
                }
            });
            
            proc.on('error', (error: Error) => {
                reject(error);
            });
        });
    }

    private async fetchVersionInfo(): Promise<VersionInfo> {
        const response = await fetch(this.updateServerUrl);
        if (!response.ok) {
            throw new Error(`Failed to fetch version info: ${response.statusText}`);
        }
        
        return await response.json() as VersionInfo;
    }

    private async fetchComponentManifest(): Promise<ComponentManifest> {
        const response = await fetch(this.componentServerUrl);
        if (!response.ok) {
            throw new Error(`Failed to fetch component manifest: ${response.statusText}`);
        }
        
        return await response.json() as ComponentManifest;
    }

    private async fetchRootComponentManifest(): Promise<RootComponentManifest> {
        const response = await fetch(this.rootComponentServerUrl);
        if (!response.ok) {
            throw new Error(`Failed to fetch root component manifest: ${response.statusText}`);
        }
        
        return await response.json() as RootComponentManifest;
    }

    private getCurrentVersion(): string {
        // Get the current extension version from package.json
        const extensionId = 'em00k.nextbuild-viewers';
        const extension = vscode.extensions.getExtension(extensionId);
        
        if (extension) {
            return extension.packageJSON.version;
        }
        
        // Fallback to stored version or default
        return this.context.globalState.get(this.currentVersionKey, '0.0.0');
    }

    private saveCurrentVersion(version: string): void {
        this.context.globalState.update(this.currentVersionKey, version);
    }

    private getComponentVersions(): Record<string, string> {
        return this.context.globalState.get(this.componentVersionKey, {});
    }

    private saveComponentVersions(versions: Record<string, string>): void {
        this.context.globalState.update(this.componentVersionKey, versions);
    }

    private getRootComponentVersions(): Record<string, string> {
        return this.context.globalState.get(this.rootComponentVersionKey, {});
    }

    private saveRootComponentVersions(versions: Record<string, string>): void {
        this.context.globalState.update(this.rootComponentVersionKey, versions);
    }

    private isNewerVersion(remote: string, local: string): boolean {
        const parseVersion = (v: string) => v.split('.').map(Number);
        const remoteVer = parseVersion(remote);
        const localVer = parseVersion(local);

        for (let i = 0; i < Math.max(remoteVer.length, localVer.length); i++) {
            const r = remoteVer[i] || 0;
            const l = localVer[i] || 0;
            if (r > l) return true;
            if (r < l) return false;
        }
        return false;
    }

    private async launchCSpectInstaller(rootFolder: string): Promise<void> {
        const installerPath = path.join(rootFolder, 'Tools', 'install_cspect.exe');
        
        try {
            // Check if installer exists
            if (!await fs.pathExists(installerPath)) {
                throw new Error(`CSpect installer not found at: ${installerPath}`);
            }
            
            // Launch installer with root folder as working directory using relative path
            const { spawn } = require('child_process');
            const process = spawn(path.join('Tools', 'install_cspect.exe'), [], {
                cwd: rootFolder,
                stdio: 'inherit'
            });
            
            // Wait for installer to complete
            await new Promise<void>((resolve, reject) => {
                process.on('close', (code: number) => {
                    if (code === 0) {
                        resolve();
                    } else {
                        reject(new Error(`CSpect installer exited with code ${code}`));
                    }
                });
                
                process.on('error', (error: Error) => {
                    reject(error);
                });
            });
            
        } catch (error) {
            console.error('Failed to launch CSpect installer:', error);
            vscode.window.showErrorMessage(`Failed to update CSpect: ${error}`);
            throw error;
        }
    }

    async autoCheckOnStartup(): Promise<void> {
        const config = vscode.workspace.getConfiguration('nextbuild-viewers');
        const autoCheck = config.get('autoCheckUpdates', true);
        
        if (!autoCheck) return;

        try {
            // Check for full extension updates
            const checkForUpdates = config.get('checkForUpdates', true);
            if (checkForUpdates) {
                const hasUpdates = await this.checkForUpdates();
                if (hasUpdates) {
                    const versionInfo = await this.fetchVersionInfo();
                    const choice = await vscode.window.showInformationMessage(
                        `NextBuild Studio update available: v${versionInfo.version}`,
                        'View Details',
                        'Remind Later'
                    );
                    
                    if (choice === 'View Details') {
                        await this.updateComponents();
                    }
                }
            }

            // Check for component updates
            const checkForComponentUpdates = config.get('checkForComponentUpdates', true);
            if (checkForComponentUpdates) {
                const hasComponentUpdates = await this.checkForComponentUpdates();
                if (hasComponentUpdates) {
                    const choice = await vscode.window.showInformationMessage(
                        'NextBuild extension component updates are available',
                        'Update Components',
                        'Remind Later'
                    );
                    
                    if (choice === 'Update Components') {
                        await this.updateExtensionComponents();
                    }
                }
            }

            // Check for root component updates
            const checkForRootUpdates = config.get('checkForRootUpdates', true);
            if (checkForRootUpdates) {
                const hasRootUpdates = await this.checkForRootUpdates();
                if (hasRootUpdates) {
                    const choice = await vscode.window.showInformationMessage(
                        'NextBuild root component updates are available',
                        'Update Components',
                        'Remind Later'
                    );
                    
                    if (choice === 'Update Components') {
                        await this.updateRootComponents();
                    }
                }
            }
        } catch (error) {
            console.error('Auto-update check failed:', error);
        }
    }

    async resetUpdateData(): Promise<void> {
        const choice = await vscode.window.showWarningMessage(
            'This will reset all update tracking data. You may see update notifications for components you already have.',
            'Reset All',
            'Reset Components Only',
            'Reset Root Components Only',
            'Cancel'
        );

        switch (choice) {
            case 'Reset All':
                await this.context.globalState.update(this.currentVersionKey, undefined);
                await this.context.globalState.update(this.componentVersionKey, undefined);
                await this.context.globalState.update(this.rootComponentVersionKey, undefined);
                vscode.window.showInformationMessage('All update tracking data has been reset.');
                break;

            case 'Reset Components Only':
                await this.context.globalState.update(this.componentVersionKey, undefined);
                vscode.window.showInformationMessage('Extension component tracking data has been reset.');
                break;

            case 'Reset Root Components Only':
                await this.context.globalState.update(this.rootComponentVersionKey, undefined);
                vscode.window.showInformationMessage('Root component tracking data has been reset.');
                break;

            case 'Cancel':
            default:
                return;
        }
    }
} 