import * as vscode from 'vscode';
import * as fs from 'fs';
import * as path from 'path';

interface ProjectTemplate {
    name: string;
    description: string;
    type: string;
    directories: string[];
    files: {
        name: string;
        template: string;
    }[];
}

export class ProjectCreatorProvider {
    private context: vscode.ExtensionContext;
    private panel: vscode.WebviewPanel | undefined;
    private templates: ProjectTemplate[] = [];

    constructor(context: vscode.ExtensionContext) {
        this.context = context;
        this.loadTemplates();
    }

    private async loadTemplates() {
        try {
            const templatesPath = path.join(this.context.extensionPath, 'data', 'templates');
            if (!fs.existsSync(templatesPath)) {
                console.warn('Templates directory not found:', templatesPath);
                return;
            }

            const files = fs.readdirSync(templatesPath);
            this.templates = [];

            for (const file of files) {
                if (file.endsWith('.json')) {
                    try {
                        const filePath = path.join(templatesPath, file);
                        const content = fs.readFileSync(filePath, 'utf8');
                        const template = JSON.parse(content) as ProjectTemplate;
                        this.templates.push(template);
                    } catch (error) {
                        console.error(`Error loading template ${file}:`, error);
                    }
                }
            }

            console.log(`Loaded ${this.templates.length} project templates`);
        } catch (error) {
            console.error('Error loading templates:', error);
        }
    }

    public async showProjectCreator() {
        try {
            // Get the current workspace folder or selected folder
            let defaultBaseDir = '';
            if (vscode.workspace.workspaceFolders && vscode.workspace.workspaceFolders.length > 0) {
                defaultBaseDir = vscode.workspace.workspaceFolders[0].uri.fsPath;
            }

            // Create or show the webview panel
            if (this.panel) {
                this.panel.reveal(vscode.ViewColumn.One);
                return;
            }

            this.panel = vscode.window.createWebviewPanel(
                'nextbuildProjectCreator',
                'NextBuild: Create New Project',
                vscode.ViewColumn.One,
                {
                    enableScripts: true,
                    retainContextWhenHidden: true
                }
            );

            this.panel.webview.html = this.getWebviewContent(defaultBaseDir);

            // Handle messages from the webview
            this.panel.webview.onDidReceiveMessage(
                async (message) => {
                    switch (message.command) {
                        case 'createProject':
                            await this.createProject(message.projectName, message.templateName, message.baseDir);
                            break;
                        case 'selectFolder':
                            await this.selectFolder();
                            break;
                    }
                },
                undefined,
                this.context.subscriptions
            );

            // Clean up when panel is disposed
            this.panel.onDidDispose(() => {
                this.panel = undefined;
            });

        } catch (error) {
            vscode.window.showErrorMessage(`Error opening project creator: ${error}`);
        }
    }

    private async selectFolder() {
        try {
            const folderUris = await vscode.window.showOpenDialog({
                canSelectFiles: false,
                canSelectFolders: true,
                canSelectMany: false,
                openLabel: 'Select Base Directory',
                title: 'Select Base Directory for New Project'
            });

            if (folderUris && folderUris[0] && this.panel) {
                this.panel.webview.postMessage({
                    command: 'updateBaseDir',
                    path: folderUris[0].fsPath
                });
            }
        } catch (error) {
            vscode.window.showErrorMessage(`Error selecting folder: ${error}`);
        }
    }

    private async createProject(projectName: string, templateName: string, baseDir: string) {
        try {
            // Validate inputs
            if (!projectName || !projectName.trim()) {
                this.showError('Project name cannot be empty');
                return;
            }

            if (!projectName.match(/^[a-zA-Z][a-zA-Z0-9_]*$/)) {
                this.showError('Project name must start with a letter and contain only letters, numbers, and underscores');
                return;
            }

            if (!baseDir || !fs.existsSync(baseDir)) {
                this.showError('Base directory does not exist');
                return;
            }

            // Find the selected template
            const template = this.templates.find(t => t.name === templateName);
            if (!template) {
                this.showError(`Template '${templateName}' not found`);
                return;
            }

            const projectDir = path.join(baseDir, projectName);

            // Check if project directory already exists
            if (fs.existsSync(projectDir)) {
                const overwrite = await vscode.window.showWarningMessage(
                    `Project directory '${projectName}' already exists. Overwrite?`,
                    'Yes', 'No'
                );
                if (overwrite !== 'Yes') {
                    return;
                }
            }

            // Create project structure from template
            await this.createProjectFromTemplate(projectName, template, projectDir);

            // Show success message
            const openProject = await vscode.window.showInformationMessage(
                `Project '${projectName}' created successfully!`,
                'Open Project', 'Close'
            );

            if (openProject === 'Open Project') {
                // Open the first file from the template
                if (template.files.length > 0) {
                    const firstFile = template.files[0];
                    const fileName = firstFile.name.replace('{projectName}', projectName);
                    const filePath = path.join(projectDir, fileName);
                    
                    if (fs.existsSync(filePath)) {
                        const uri = vscode.Uri.file(filePath);
                        await vscode.commands.executeCommand('vscode.open', uri);
                    }
                }
            }

            // Close the creator panel
            if (this.panel) {
                this.panel.dispose();
            }

        } catch (error) {
            this.showError(`Error creating project: ${error}`);
        }
    }

    private async createProjectFromTemplate(projectName: string, template: ProjectTemplate, projectDir: string) {
        // Create base project directory
        if (!fs.existsSync(projectDir)) {
            fs.mkdirSync(projectDir, { recursive: true });
        }

        // Create directories from template
        for (const dir of template.directories) {
            const dirPath = path.join(projectDir, dir);
            if (!fs.existsSync(dirPath)) {
                fs.mkdirSync(dirPath, { recursive: true });
            }
        }

        // Create files from template
        for (const file of template.files) {
            const fileName = file.name.replace('{projectName}', projectName);
            const filePath = path.join(projectDir, fileName);
            
            // Ensure directory exists for the file
            const fileDir = path.dirname(filePath);
            if (!fs.existsSync(fileDir)) {
                fs.mkdirSync(fileDir, { recursive: true });
            }

            // Process template content
            const content = file.template
                .replace(/{projectName}/g, projectName)
                .replace(/\\n/g, '\n');
            
            fs.writeFileSync(filePath, content, 'utf8');
        }
    }



    private getTemplateStructureHtml(): string {
        if (this.templates.length === 0) {
            return '<p>No templates available</p>';
        }

        const template = this.templates[0];
        let html = `<strong>${template.name}:</strong><ul>`;
        
        // Show directories
        template.directories.forEach(dir => {
            html += `<li>${dir}/ (folder)</li>`;
        });
        
        // Show files
        template.files.forEach(file => {
            const fileName = file.name.replace('{projectName}', 'ProjectName');
            html += `<li>${fileName}</li>`;
        });
        
        html += '</ul>';
        return html;
    }

    private showError(message: string) {
        vscode.window.showErrorMessage(message);
        if (this.panel) {
            this.panel.webview.postMessage({
                command: 'showError',
                message: message
            });
        }
    }

    private getWebviewContent(defaultBaseDir: string): string {
        return `<!DOCTYPE html>
<html lang="en">
<head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <title>Create NextBuild Project</title>
    <style>
        body {
            font-family: var(--vscode-font-family);
            font-size: var(--vscode-font-size);
            color: var(--vscode-foreground);
            background-color: var(--vscode-editor-background);

        }
        .form-group {
            margin-bottom: 10px;
        }
        label {
            display: block;
            margin-bottom: 5px;
            font-weight: bold;
        }
        input[type="text"], select {
            width: 100%;
            padding: 8px;
            border: 1px solid var(--vscode-input-border);
            background-color: var(--vscode-input-background);
            color: var(--vscode-input-foreground);
            border-radius: 3px;
            font-size: var(--vscode-font-size);
        }
        button {
            background-color: var(--vscode-button-background);
            color: var(--vscode-button-foreground);
            border: none;
            padding: 10px 20px;
            border-radius: 3px;
            cursor: pointer;
            font-size: var(--vscode-font-size);
            margin-right: 10px;
        }
        button:hover {
            background-color: var(--vscode-button-hoverBackground);
        }
        .secondary-button {
            background-color: var(--vscode-button-secondaryBackground);
            color: var(--vscode-button-secondaryForeground);
        }
        .secondary-button:hover {
            background-color: var(--vscode-button-secondaryHoverBackground);
        }
        .error {
            color: var(--vscode-errorForeground);
            background-color: var(--vscode-inputValidation-errorBackground);
            border: 1px solid var(--vscode-inputValidation-errorBorder);
            padding: 10px;
            border-radius: 3px;
            margin-bottom: 20px;
            display: none;
        }
        .folder-input {
            display: flex;
            gap: 10px;
            align-items: center;
        }
        .folder-input input {
            flex: 1;
        }
        .folder-input button {
            padding: 8px 15px;
            margin: 0;
        }
        .project-info {
            background-color: var(--vscode-textBlockQuote-background);
            border-left: 4px solid var(--vscode-textBlockQuote-border);
            padding: 15px;
            margin-bottom: 20px;
            border-radius: 3px;
        }
        .project-info h3 {
            margin-top: 0;
            color: var(--vscode-textPreformat-foreground);
        }
        .project-info ul {
            margin: 10px 0;
            padding-left: 20px;
        }
    </style>
</head>
<body>
    <h1>Create NextBuild Project</h1>
    
    <div id="error-message" class="error"></div>
    
    <form id="project-form">
        <div class="form-group">
            <label for="project-name">Project Name:</label>
            <input type="text" id="project-name" name="projectName" placeholder="MyProject" required>
        </div>
        
        <div class="form-group">
            <label for="project-type">Project Template:</label>
            <select id="project-type" name="templateName">
                ${this.templates.map(template => 
                    `<option value="${template.name}">${template.name}</option>`
                ).join('')}
            </select>
        </div>
        
        <div class="form-group">
            <label>Template Description:</label>
            <div id="template-description">
                ${this.templates.length > 0 ? this.templates[0].description : 'No templates available'}
            </div>
        </div>
        
        <div class="form-group">
            <label for="base-dir">Base Sources Directory:</label>
            <div class="folder-input">
                <input type="text" id="base-dir" name="baseDir" value="${defaultBaseDir}" required>
                <button type="button" class="secondary-button" onclick="selectFolder()">Browse...</button>
            </div>
        </div>
        
        <div class="project-info">
            <h3>Project Structure:</h3>
            <div id="structure-info">
                ${this.getTemplateStructureHtml()}
            </div>
        </div>
        
        <button type="submit">Create Project</button>
        <button type="button" class="secondary-button" onclick="window.close()">Cancel</button>
    </form>

    <script>
        const vscode = acquireVsCodeApi();
        
        // Handle form submission
        document.getElementById('project-form').addEventListener('submit', (e) => {
            e.preventDefault();
            
            const formData = new FormData(e.target);
            const projectName = formData.get('projectName').trim();
            const templateName = formData.get('templateName');
            const baseDir = formData.get('baseDir').trim();
            
            if (!projectName) {
                showError('Project name is required');
                return;
            }
            
            if (!baseDir) {
                showError('Base directory is required');
                return;
            }
            
            vscode.postMessage({
                command: 'createProject',
                projectName: projectName,
                templateName: templateName,
                baseDir: baseDir
            });
        });
        
        // Template data from extension
        const templates = ${JSON.stringify(this.templates)};
        
        // Handle template type change
        document.getElementById('project-type').addEventListener('change', (e) => {
            const selectedTemplate = templates.find(t => t.name === e.target.value);
            if (selectedTemplate) {
                // Update description
                document.getElementById('template-description').textContent = selectedTemplate.description;
                
                // Update structure info
                const structureInfo = document.getElementById('structure-info');
                let html = \`<strong>\${selectedTemplate.name}:</strong><ul>\`;
                
                // Show directories
                selectedTemplate.directories.forEach(dir => {
                    html += \`<li>\${dir}/ (folder)</li>\`;
                });
                
                // Show files
                selectedTemplate.files.forEach(file => {
                    const fileName = file.name.replace('{projectName}', 'ProjectName');
                    html += \`<li>\${fileName}</li>\`;
                });
                
                html += '</ul>';
                structureInfo.innerHTML = html;
            }
        });
        
        // Handle folder selection
        function selectFolder() {
            vscode.postMessage({
                command: 'selectFolder'
            });
        }
        
        // Handle messages from extension
        window.addEventListener('message', event => {
            const message = event.data;
            switch (message.command) {
                case 'updateBaseDir':
                    document.getElementById('base-dir').value = message.path;
                    break;
                case 'showError':
                    showError(message.message);
                    break;
            }
        });
        
        function showError(message) {
            const errorDiv = document.getElementById('error-message');
            errorDiv.textContent = message;
            errorDiv.style.display = 'block';
            setTimeout(() => {
                errorDiv.style.display = 'none';
            }, 5000);
        }
        
        // Focus on project name input
        document.getElementById('project-name').focus();
    </script>
</body>
</html>`;
    }
}