import * as vscode from 'vscode';
import { execFile } from 'child_process';
import * as path from 'path';
import { PythonDetector } from './pythonDetector';

/**
 * Class that handles linting NextBuild source files
 */
export class NextBuildLinter {
    private diagnosticCollection: vscode.DiagnosticCollection;

    constructor(context: vscode.ExtensionContext) {
        // Create diagnostic collection for the linter
        this.diagnosticCollection = vscode.languages.createDiagnosticCollection('nextbuild');

        // Register linter events
        context.subscriptions.push(
            vscode.workspace.onDidSaveTextDocument(this.runLint, this),
            vscode.workspace.onDidOpenTextDocument(this.runLint, this),
            // Clean up diagnostics when the file is closed
            vscode.workspace.onDidCloseTextDocument(doc => this.diagnosticCollection.delete(doc.uri)),
            // Register the collection
            this.diagnosticCollection
        );
    }

    /**
     * Run the linter on the specified document
     */
    public runLint(document: vscode.TextDocument): void {
        // Only lint .bas files
        if (document.languageId !== 'nextbuild' || !document.fileName.toLowerCase().endsWith('.bas')  || document.fileName.toLowerCase().endsWith('nextlib.bas')) {
            return;
        }

        const config = vscode.workspace.getConfiguration('nextbuild-viewers.linting');
        if (!config.get('enable')) {
            // Clear diagnostics when linting is disabled
            this.diagnosticCollection.clear();
            return;
        }
      
        const pythonPath = config.get<string>('pythonPath', 'python');
        const compilerPathSetting = config.get<string>('compilerPath', '');
        const baseCompilerArgs = config.get<string>('compilerArgs', 'parse').split(' ').filter(arg => arg.length > 0);
        const rootFolderForIncludes = config.get<string>('rootFolderForIncludes', '');
        const hiddenWarnings = config.get<string[]>('hiddenWarnings', []);
        const hideAllWarnings = config.get<boolean>('hideAllWarnings', false);
      
        if (!compilerPathSetting) {
            vscode.window.showWarningMessage('Compiler path for linting is not set. Please configure `nextbuild-viewers.linting.compilerPath`.');
            return;
        }

        // Resolve the full compiler path using rootFolderForIncludes if set
        let fullCompilerPath: string;
        if (rootFolderForIncludes && rootFolderForIncludes.trim() !== '') {
            // If rootFolderForIncludes is set, resolve compilerPath relative to it
            fullCompilerPath = path.resolve(rootFolderForIncludes.trim(), compilerPathSetting);
        } else if (path.isAbsolute(compilerPathSetting)) {
            // If compilerPath is absolute, use it as-is
            fullCompilerPath = compilerPathSetting;
        } else {
            // If compilerPath is relative and no rootFolder, resolve relative to workspace
            const workspaceFolder = vscode.workspace.getWorkspaceFolder(document.uri);
            const baseDir = workspaceFolder ? workspaceFolder.uri.fsPath : path.dirname(document.fileName);
            fullCompilerPath = path.resolve(baseDir, compilerPathSetting);
        }

        // Log the command being executed (helpful for debugging)
        //console.log(`Running linter on ${document.fileName}`);
        //console.log(`Using Python: ${pythonPath}`);
        //console.log(`Script path: ${compilerPathSetting}`);
        //console.log(`Full compiler path: ${fullCompilerPath}`);
        //console.log(`Base Arguments: ${baseCompilerArgs.join(' ')}`);

        // Prepare final compiler arguments
        const finalCompilerArgs = [fullCompilerPath, ...baseCompilerArgs];
        
        // Add include path if rootFolderForIncludes is set
        if (rootFolderForIncludes && rootFolderForIncludes.trim() !== '') {
            const scriptsIncludePath = path.join(rootFolderForIncludes.trim(), 'Scripts');
            finalCompilerArgs.push('-I');
            finalCompilerArgs.push(scriptsIncludePath);
          //  console.log(`Added include path: -I "${scriptsIncludePath}"`);
        }

        // Add the document to be linted as the last argument for the script itself
        finalCompilerArgs.push(document.fileName);

        //console.log(`Final Script + Args for Python: ${finalCompilerArgs.join(' ')}`);
        
        this.diagnosticCollection.clear(); // clear the diagnostics

        // Use a safer working directory - either the workspace root or document directory
        const workspaceFolder = vscode.workspace.getWorkspaceFolder(document.uri);
        const safeCwd = workspaceFolder ? workspaceFolder.uri.fsPath : path.dirname(document.fileName);

        execFile(pythonPath, finalCompilerArgs, { cwd: safeCwd }, (err, stdout, stderr) => {
            // Maps URI.toString() to Diagnostic[]
            const diagnosticsMap = new Map<string, vscode.Diagnostic[]>();

            const addDiagnosticToMap = (fileUri: vscode.Uri, diagnostic: vscode.Diagnostic) => {
                const uriString = fileUri.toString();
                const existing = diagnosticsMap.get(uriString) || [];
                existing.push(diagnostic);
                diagnosticsMap.set(uriString, existing);
            };

            const parseOutputAndCreateDiagnostics = (output: string, streamName: string, sourceDocUri: vscode.Uri) => {
                //console.log(`[Linter] Parsing ${streamName}. Input (first 200 chars): ${output.substring(0,200)}...`);
                // Use more robust line splitting for \r\n and \n
                const lines = output.split(/\r?\n/);
                
                lines.forEach(line => {
                    const trimmedLine = line.trim(); // Trim whitespace to avoid issues with empty lines or leading/trailing spaces
                    if (!trimmedLine) { return;} // Skip empty or whitespace-only lines

                    //console.log(`[Linter] Attempting to match line: "${trimmedLine}"`); // Log each line being processed

                    // Match format: filename:line:column: severity: message
                    // Column is optional
                    const regex = /^(.*?):(\d+)(?::(\d+))?:\s*(warning|error):\s*(.*)$/;
                    const match = trimmedLine.match(regex);

                    if (match) {
                        const [, filePathStr, lineNumStr, columnStr, severityStr, messageText] = match;
                        
                        let errorFileUri: vscode.Uri;
                        try {
                            if (path.isAbsolute(filePathStr)) {
                                errorFileUri = vscode.Uri.file(filePathStr);
                            } else {
                                const workspaceFolder = vscode.workspace.getWorkspaceFolder(sourceDocUri) || 
                                                      (vscode.workspace.workspaceFolders && vscode.workspace.workspaceFolders[0]);
                                const baseDir = workspaceFolder ? workspaceFolder.uri.fsPath : path.dirname(sourceDocUri.fsPath);
                                errorFileUri = vscode.Uri.file(path.resolve(baseDir, filePathStr));
                            }

                            const lineNumber = parseInt(lineNumStr, 10);
                            if (isNaN(lineNumber) || lineNumber < 1) {
                                console.warn(`[Linter] Invalid line number: ${lineNumStr} in error: ${line}`);
                                return;
                            }

                            // Column is optional, if not provided default to 0
                            const columnNumber = columnStr ? parseInt(columnStr, 10) - 1 : 0;
                            
                            const zeroBasedLineNumber = lineNumber - 1;
                            //console.log(`[Linter] Matched: file=${filePathStr}, line=${lineNumber}, column=${columnNumber}, severity=${severityStr}`);

                            // Create a range that covers the problematic code
                            // If we have a column, use it, otherwise highlight the line
                            let range: vscode.Range | undefined;
                            
                                        // Specific handling for implicit type warnings: highlight the variable name in quotes
            const implicitTypeMatch = messageText.match(/Using default implicit type '.*' for '([^']+)'/i);
            if (implicitTypeMatch && implicitTypeMatch[1]) {
                const targetWord = implicitTypeMatch[1];
                const line = document.lineAt(zeroBasedLineNumber);
                const lineText = line.text;
                
                // Use regex to find word boundaries - this prevents matching 'p' inside 'print'
                const wordBoundaryRegex = new RegExp(`\\b${targetWord.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}\\b`);
                const match = lineText.match(wordBoundaryRegex);
                
                if (match && match.index !== undefined) {
                    const startIndex = match.index;
                    const endIndex = startIndex + targetWord.length;
                    range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, endIndex);
                } else {
                    // Fallback: try simple indexOf as last resort
                    const startIndex = lineText.indexOf(targetWord);
                    if (startIndex !== -1) {
                        const endIndex = startIndex + targetWord.length;
                        range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, endIndex);
                    } else {
                        // Use column if available, otherwise start of line
                        range = new vscode.Range(zeroBasedLineNumber, columnNumber, zeroBasedLineNumber, columnNumber > 0 ? columnNumber + 1 : 0);
                    }
                }
                            } else if (messageText.includes('Redundant operation ABS')) {
                                // Highlight 'ABS' in "Redundant operation ABS"
                                const targetWord = 'ABS';
                                const line = document.lineAt(zeroBasedLineNumber);
                                const lineText = line.text;
                                const startIndex = lineText.indexOf(targetWord);
                                if (startIndex !== -1) {
                                    range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                } else {
                                    range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                }
                            } else if (messageText.includes('Sign of unsigned value is always 0 or 1')) {
                                // Highlight 'SGN' in "Sign of unsigned value is always 0 or 1"
                                const targetWord = 'SGN';
                                const line = document.lineAt(zeroBasedLineNumber);
                                const lineText = line.text;
                                const startIndex = lineText.indexOf(targetWord);
                                if (startIndex !== -1) {
                                    range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                } else {
                                    range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                }
                            } else if (messageText.includes('FOR variable should be')) {
                                // Highlight the wrong variable in "FOR variable should be 'i' instead of 'x'"
                                const forVariableMatch = messageText.match(/FOR variable should be '[^']+' instead of '([^']+)'/);
                                                                 if (forVariableMatch && forVariableMatch[1]) {
                                     const targetWord = forVariableMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                                         } else if (messageText.includes('Unexpected token') && messageText.includes('<NUMBER>')) {
                // Highlight the number in "Syntax Error. Unexpected token '222.0' <NUMBER>"
                const numberMatch = messageText.match(/Unexpected token '([^']+)'/);
                if (numberMatch && numberMatch[1]) {
                    const targetWord = numberMatch[1];
                    const line = document.lineAt(zeroBasedLineNumber);
                    const lineText = line.text;
                    
                    // Try to find exact match first
                    let startIndex = lineText.indexOf(targetWord);
                    
                    // If not found and target ends with .0, try without .0
                    if (startIndex === -1 && targetWord.endsWith('.0')) {
                        const baseNumber = targetWord.slice(0, -2); // Remove '.0'
                        startIndex = lineText.indexOf(baseNumber);
                        if (startIndex !== -1) {
                            // Highlight the base number instead
                            range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + baseNumber.length);
                        } else {
                            range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                        }
                    } else if (startIndex !== -1) {
                        range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                    } else {
                        range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                    }
                } else {
                    range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                }
                             } else if (messageText.includes('illegal preprocessor character')) {
                                 // Highlight '\' in "illegal preprocessor character '\'"
                                 const charMatch = messageText.match(/illegal preprocessor character '([^']+)'/);
                                 if (charMatch && charMatch[1]) {
                                     const targetWord = charMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('Syntax Error. Unexpected token') && !messageText.includes('<NUMBER>')) {
                                 // Highlight the token in "Syntax Error. Unexpected token 'FOR' <FOR>"
                                 const tokenMatch = messageText.match(/Syntax Error\. Unexpected token '([^']+)'/);
                                 if (tokenMatch && tokenMatch[1]) {
                                     const targetWord = tokenMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('don\'t have the same number of dimensions')) {
                                 // Highlight the first array in "Arrays 'array1' and 'array2' don't have the same number of dimensions"
                                 const arrayMatch = messageText.match(/Arrays '([^']+)' and '([^']+)' don't have the same number of dimensions/);
                                 if (arrayMatch && arrayMatch[1]) {
                                     const targetWord = arrayMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('missing type declaration for')) {
                                 // Highlight the variable in "strict mode: missing type declaration for 'variable'"
                                 const typeMatch = messageText.match(/missing type declaration for '([^']+)'/);
                                 if (typeMatch && typeMatch[1]) {
                                     const targetWord = typeMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('is a SUB not a FUNCTION')) {
                                 // Highlight the function name in "'funcName' is a SUB not a FUNCTION"
                                 const subMatch = messageText.match(/'([^']+)' is a SUB not a FUNCTION/);
                                 if (subMatch && subMatch[1]) {
                                     const targetWord = subMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('It\'s not a variable')) {
                                 // Highlight the name in "Cannot assign a value to 'name'. It's not a variable"
                                 const assignMatch = messageText.match(/Cannot assign a value to '([^']+)'\. It's not a variable/);
                                 if (assignMatch && assignMatch[1]) {
                                     const targetWord = assignMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('Invalid string numeric constant') && messageText.includes('evaluated as 0')) {
                                 // Highlight the invalid constant in "Invalid string numeric constant 'abc123' evaluated as 0"
                                 const valMatch = messageText.match(/Invalid string numeric constant '([^']+)' evaluated as 0/);
                                 if (valMatch && valMatch[1]) {
                                     const targetWord = valMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('STEP value is 0 and FOR might loop forever')) {
                                 // Highlight 'STEP' in "STEP value is 0 and FOR might loop forever"
                                 const targetWord = 'STEP';
                                 const line = document.lineAt(zeroBasedLineNumber);
                                 const lineText = line.text;
                                 const startIndex = lineText.indexOf(targetWord);
                                 if (startIndex !== -1) {
                                     range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('FOR without NEXT')) {
                                 // Highlight 'FOR' in "FOR without NEXT"
                                 const targetWord = 'FOR';
                                 const line = document.lineAt(zeroBasedLineNumber);
                                 const lineText = line.text;
                                 const startIndex = lineText.indexOf(targetWord);
                                 if (startIndex !== -1) {
                                     range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('IF without ENDIF') || messageText.includes('Missing ENDIF')) {
                                 // Highlight 'IF' in "IF without ENDIF" or "Missing ENDIF"
                                 const targetWord = 'IF';
                                 const line = document.lineAt(zeroBasedLineNumber);
                                 const lineText = line.text;
                                 const startIndex = lineText.indexOf(targetWord);
                                 if (startIndex !== -1) {
                                     range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('DO loop not closed') || messageText.includes('DO without LOOP')) {
                                 // Highlight 'DO' in "DO loop not closed" or "DO without LOOP"
                                 const targetWord = 'DO';
                                 const line = document.lineAt(zeroBasedLineNumber);
                                 const lineText = line.text;
                                 const startIndex = lineText.indexOf(targetWord);
                                 if (startIndex !== -1) {
                                     range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('WHILE without WEND')) {
                                 // Highlight 'WHILE' in "WHILE without WEND"
                                 const targetWord = 'WHILE';
                                 const line = document.lineAt(zeroBasedLineNumber);
                                 const lineText = line.text;
                                 const startIndex = lineText.indexOf(targetWord);
                                 if (startIndex !== -1) {
                                     range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('SELECT without END SELECT')) {
                                 // Highlight 'SELECT' in "SELECT without END SELECT"
                                 const targetWord = 'SELECT';
                                 const line = document.lineAt(zeroBasedLineNumber);
                                 const lineText = line.text;
                                 const startIndex = lineText.indexOf(targetWord);
                                 if (startIndex !== -1) {
                                     range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('SUB without END SUB') || messageText.includes('FUNCTION without END FUNCTION')) {
                                 // Highlight 'SUB' or 'FUNCTION' in structure errors
                                 const subMatch = messageText.match(/\b(SUB|FUNCTION)\b/i);
                                 if (subMatch) {
                                     const targetWord = subMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('is neither array nor function')) {
                                 // Highlight the identifier in "'identifier' is neither array nor function"
                                 const neitherMatch = messageText.match(/'([^']+)' is neither array nor function/);
                                 if (neitherMatch && neitherMatch[1]) {
                                     const targetWord = neitherMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('Undeclared identifier')) {
                                 // Highlight the undeclared identifier in "Undeclared identifier 'variable'"
                                 const undeclaredMatch = messageText.match(/Undeclared identifier '([^']+)'/);
                                 if (undeclaredMatch && undeclaredMatch[1]) {
                                     const targetWord = undeclaredMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                             } else if (messageText.includes('Type mismatch')) {
                                 // For type mismatch errors, try to find the problematic expression
                                 const typeMismatchMatch = messageText.match(/Type mismatch.*'([^']+)'/);
                                 if (typeMismatchMatch && typeMismatchMatch[1]) {
                                     const targetWord = typeMismatchMatch[1];
                                     const line = document.lineAt(zeroBasedLineNumber);
                                     const lineText = line.text;
                                     const startIndex = lineText.indexOf(targetWord);
                                     if (startIndex !== -1) {
                                         range = new vscode.Range(zeroBasedLineNumber, startIndex, zeroBasedLineNumber, startIndex + targetWord.length);
                                     } else {
                                         range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                     }
                                 } else {
                                     range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                 }
                            } else if (columnStr) {
                                // General handling when column is provided: try to find word boundaries
                                // Try to get the line from the document if it's the current document
                                if (document.uri.fsPath === errorFileUri.fsPath) {
                                    // If the column is valid, create a range for the word at that position
                                    if (columnNumber >= 0 && zeroBasedLineNumber < document.lineCount) {
                                        const line = document.lineAt(zeroBasedLineNumber);
                                        const lineText = line.text;
                                        
                                        // Find the start of the word at or before the column
                                        let wordStartCol = columnNumber;
                                        while (wordStartCol > 0 && 
                                               /\w/.test(lineText[wordStartCol - 1])) {
                                            wordStartCol--;
                                        }

                                        // Find the end of the word at or after the column
                                        let wordEndCol = columnNumber;
                                         while (wordEndCol < lineText.length && 
                                                /\w/.test(lineText[wordEndCol])) {
                                            wordEndCol++;
                                        }
                                        
                                        // If we found a word that includes the column, use that range
                                        if (wordEndCol > wordStartCol) {
                                            range = new vscode.Range(zeroBasedLineNumber, wordStartCol, zeroBasedLineNumber, wordEndCol);
                                        } else {
                                             // Fallback if not a word character at column, highlight a small range
                                            range = new vscode.Range(zeroBasedLineNumber, columnNumber, zeroBasedLineNumber, columnNumber + 1);
                                        }
                                    } else {
                                        // Invalid column, highlight the line
                                        range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                                    }
                                } else {
                                    // Different file, we don't have access to its content, use simple range based on column
                                     range = new vscode.Range(zeroBasedLineNumber, columnNumber, zeroBasedLineNumber, columnNumber + 1);
                                }
                            } else {
                                // No column specified, highlight the beginning of the line
                                range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                            }
                            
                            // Ensure range is defined before using it
                            if (!range) {
                                range = new vscode.Range(zeroBasedLineNumber, 0, zeroBasedLineNumber, 0);
                            }

                            const diagnostic = new vscode.Diagnostic(
                                range,
                                messageText.trim(),
                                severityStr.toLowerCase() === 'error' ? vscode.DiagnosticSeverity.Error : vscode.DiagnosticSeverity.Warning
                            );
                            diagnostic.source = 'NextBuild';
                            
                            // Extract warning code if present (e.g., [W100])
                            const warningCodeMatch = messageText.match(/\[W(\d+)\]/);
                            if (warningCodeMatch) {
                                diagnostic.code = { 
                                    value: `W${warningCodeMatch[1]}`, 
                                    target: errorFileUri.with({ fragment: `L${lineNumber}${columnStr ? `C${columnStr}` : ''}` })
                                };
                            } else {
                                diagnostic.code = { 
                                    value: severityStr.toUpperCase(), 
                                    target: errorFileUri.with({ fragment: `L${lineNumber}${columnStr ? `C${columnStr}` : ''}` })
                                };
                            }
                            
                            // Apply warning filtering
                            const isWarning = severityStr.toLowerCase() === 'warning';
                            const shouldHide = isWarning && (
                                hideAllWarnings || 
                                (warningCodeMatch && hiddenWarnings.includes(`W${warningCodeMatch[1]}`))
                            );
                            
                            if (!shouldHide) {
                                addDiagnosticToMap(errorFileUri, diagnostic);
                            }

                        } catch (e) {
                            console.error(`[Linter] Error processing diagnostic line "${line}":`, e);
                        }
                    }
                });
            };

            // Always parse stdout and stderr first, regardless of the 'err' object from execFile.
            // Compilers often output to these streams and then exit with an error code.
            if (stdout) {
                parseOutputAndCreateDiagnostics(stdout, "stdout", document.uri);
            }
            if (stderr) {
                parseOutputAndCreateDiagnostics(stderr, "stderr", document.uri);
            }

            // Now, handle the execFile 'err' object, but only if no specific diagnostics were found.
            if (err) {
                console.error('[Linter] execFile error object present:', err);
                // If it's a setup error (ENOENT), always show it as it prevents linting.
                if (err.code === 'ENOENT') { 
                    const range = new vscode.Range(0, 0, 0, 0);
                    
                    if (err.message.includes(pythonPath) || err.message.toLowerCase().includes('python')) {
                        // Python executable not found - show helpful error with detection options
                        const msg = `Python executable not found (path: "${pythonPath}"). Click to configure Python path.`;
                        const setupDiagnostic = new vscode.Diagnostic(range, msg, vscode.DiagnosticSeverity.Error);
                        setupDiagnostic.source = 'NextBuild (Setup)';
                        addDiagnosticToMap(document.uri, setupDiagnostic);
                        
                        // Show helpful error dialog with options
                        PythonDetector.showPythonNotFoundError(pythonPath);
                    } else {
                        // Compiler script not found
                        const msg = `Compiler script not found. Check 'nextbuild-viewers.linting.compilerPath' setting. Attempted: "${compilerPathSetting}".`;
                        const setupDiagnostic = new vscode.Diagnostic(range, msg, vscode.DiagnosticSeverity.Error);
                        setupDiagnostic.source = 'NextBuild (Setup)';
                        addDiagnosticToMap(document.uri, setupDiagnostic);
                    }
                } else if (diagnosticsMap.size === 0) { 
                    // Only show generic execFile error if no specific errors were parsed from stdout/stderr.
                    const range = new vscode.Range(0, 0, 0, 0);
                    const genericDiagnostic = new vscode.Diagnostic(range, `Linting process failed (exit code ${err.code || 'unknown'}). Error: ${err.message}`, vscode.DiagnosticSeverity.Error);
                    genericDiagnostic.source = 'NextBuild (Process)';
                    addDiagnosticToMap(document.uri, genericDiagnostic); // Add to current document
                }
            }

            // Update the diagnostic collection for all affected files
            const allUrisPreviouslyWithDiagnostics = new Set<string>();
            this.diagnosticCollection.forEach((_, uri) => allUrisPreviouslyWithDiagnostics.add(uri.toString()));

            diagnosticsMap.forEach((diags, uriString) => {
                this.diagnosticCollection.set(vscode.Uri.parse(uriString), diags);
                allUrisPreviouslyWithDiagnostics.delete(uriString); // Remove from set if it still has diagnostics
            });

            // Clear diagnostics for any file that was part of this lint run but no longer has errors
            allUrisPreviouslyWithDiagnostics.forEach(uriString => {
                // Only clear if it's the document being linted or an include that might have been fixed.
                // This logic might need refinement if includes can span widely.
                // For now, if a file had errors and is no longer in diagnosticsMap, clear it.
                this.diagnosticCollection.delete(vscode.Uri.parse(uriString));
            });
        });
    }
} 