import * as vscode from 'vscode';
import * as fs from 'fs';
import * as path from 'path';
import { PSGImporter, PSGImportResult } from './ayfxPSGImport';

export class AyfxAudioEditorProvider implements vscode.CustomReadonlyEditorProvider {
    public static readonly viewType = 'nextbuild-viewers.ayfxAudioEditor';

    constructor(private readonly context: vscode.ExtensionContext) {}

    public async openCustomDocument(
        uri: vscode.Uri,
        openContext: vscode.CustomDocumentOpenContext,
        _token: vscode.CancellationToken
    ): Promise<vscode.CustomDocument> {
        return { uri, dispose: () => {} };
    }

    public async resolveCustomEditor(
        document: vscode.CustomDocument,
        webviewPanel: vscode.WebviewPanel,
        _token: vscode.CancellationToken
    ): Promise<void> {
        webviewPanel.webview.options = {
            enableScripts: true,
            localResourceRoots: [
                vscode.Uri.file(path.join(this.context.extensionPath, 'src', 'ayfx'))
            ]
        };

        const ayfxProvider = new AyfxAudioProvider(this.context);
        webviewPanel.webview.html = ayfxProvider.getWebviewContent(webviewPanel.webview);

        // Handle messages from the webview
        webviewPanel.webview.onDidReceiveMessage(
            message => this.handleMessage(message, webviewPanel, document),
            undefined,
            this.context.subscriptions
        );

        // Load the initial file if it exists and is not a virtual document
        if (document.uri.scheme === 'file' && fs.existsSync(document.uri.fsPath)) {
            const fileData = fs.readFileSync(document.uri.fsPath);
            webviewPanel.webview.postMessage({
                command: 'loadBank',
                data: Array.from(fileData)
            });
        }
        // For virtual/untitled documents, the editor will start with an empty bank
    }

    private async handleMessage(message: any, webviewPanel: vscode.WebviewPanel, document: vscode.CustomDocument) {
        switch (message.command) {
            case 'showHelp':
                // Open the help system with the specified keyword
                if (message.keyword) {
                    try {
                        await vscode.commands.executeCommand('nextbuild-viewers.showKeywordHelp', message.keyword);
                    } catch (error) {
                        console.error('Failed to open help:', error);
                        vscode.window.showErrorMessage('Help system not available');
                    }
                }
                break;

            case 'saveDialog':
                const saveUri = await vscode.window.showSaveDialog({
                    defaultUri: vscode.Uri.file(message.defaultName),
                    filters: {
                        'AYFX Bank Files': ['afb'],
                        'All Files': ['*']
                    }
                });
                if (saveUri) {
                    // Ensure we're writing the raw bytes correctly
                    const buffer = Buffer.from(new Uint8Array(message.data));
                    fs.writeFileSync(saveUri.fsPath, buffer);
                    vscode.window.showInformationMessage(`Saved ${saveUri.fsPath}`);
                }
                break;

            case 'saveFile':
                const saveFileUri = await vscode.window.showSaveDialog({
                    defaultUri: vscode.Uri.file(message.filename),
                    filters: message.filename.endsWith('.afx') ? 
                        { 'AYFX Effect Files': ['afx'], 'All Files': ['*'] } :
                        { 'Wave Files': ['wav'], 'All Files': ['*'] }
                });
                if (saveFileUri) {
                    // Ensure we're writing the raw bytes correctly
                    const buffer = Buffer.from(new Uint8Array(message.data));
                    fs.writeFileSync(saveFileUri.fsPath, buffer);
                    vscode.window.showInformationMessage(`Saved ${saveFileUri.fsPath}`);
                }
                break;

            case 'loadCollection':
                const collectionPath = path.join(this.context.extensionPath, 'src', 'ayfx', 'assets', `${message.collection}.afb`);
                if (fs.existsSync(collectionPath)) {
                    const collectionData = fs.readFileSync(collectionPath);
                    webviewPanel.webview.postMessage({
                        command: 'loadBank',
                        data: Array.from(collectionData)
                    });
                } else {
                    vscode.window.showErrorMessage(`Collection ${message.collection} not found at ${collectionPath}`);
                }
                break;

            case 'loadPSG':
                await this.handlePSGImport(webviewPanel);
                break;

            case 'handleDrop':
                // Handle drag and drop files
                for (const fileName of message.files) {
                    try {
                        const filePath = path.resolve(fileName);
                        if (fs.existsSync(filePath)) {
                            const fileData = fs.readFileSync(filePath);
                            const isEffect = fileName.toLowerCase().endsWith('.afx');
                            
                            webviewPanel.webview.postMessage({
                                command: isEffect ? 'loadEffect' : 'loadBank',
                                data: Array.from(fileData),
                                name: isEffect ? path.basename(fileName, '.afx') : undefined
                            });
                        }
                    } catch (error) {
                        vscode.window.showErrorMessage(`Failed to load ${fileName}: ${error}`);
                    }
                }
                break;

            case 'showError':
                // Show error message from webview
                if (message.message) {
                    vscode.window.showErrorMessage(message.message);
                }
                break;
        }
    }

    private async handlePSGImport(webviewPanel: vscode.WebviewPanel) {
        try {
            const psgImporter = new PSGImporter(this.context);
            const importResult = await psgImporter.showImportDialog();
            
            if (importResult) {
                const { options, psgData, filePath } = importResult;
                
                // Show progress indicator
                await vscode.window.withProgress({
                    location: vscode.ProgressLocation.Notification,
                    title: "Converting PSG to AYFX",
                    cancellable: false
                }, async (progress) => {
                    progress.report({ message: "Parsing PSG data..." });
                    
                    // Convert PSG data to AYFX format
                    const { channelData, effectName } = PSGImporter.convertPSGToAYFX(psgData, options);
                    
                    progress.report({ message: "Sending to editor..." });
                    
                    // Send the converted data to the webview
                    webviewPanel.webview.postMessage({
                        command: 'loadPSGData',
                        channelData: channelData,
                        effectName: effectName,
                        options: options
                    });
                    
                    // Small delay to show completion
                    await new Promise(resolve => setTimeout(resolve, 500));
                });
                
                // Show success message
                vscode.window.showInformationMessage(
                    `PSG imported: ${options.sourceChannels.length} channels, frames ${options.startFrame}-${options.endFrame}`
                );
            }
        } catch (error) {
            console.error('PSG import failed:', error);
            vscode.window.showErrorMessage(`PSG import failed: ${error}`);
        }
    }
}

export class AyfxAudioProvider {
    constructor(private readonly context: vscode.ExtensionContext) {}

    public getWebviewContent(webview: vscode.Webview): string {
        const ayfxPath = path.join(this.context.extensionPath, 'src', 'ayfx');
        const viewPath = path.join(ayfxPath, 'view');

        // Simple debounce implementation
        const debounceFunction = `
function debounce(func, wait) {
    let timeout;
    return function executedFunction(...args) {
        const later = () => {
            clearTimeout(timeout);
            func(...args);
        };
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
    };
}
`;

        // Bundle required backend JS files
        const filesToBundle = [
            'lib/$.js', 
            'lib/Hooks.js', 
            'lib/encode.js', 
            'lib/dnd.js',
            'lib/save.js',
            'audio/ayplay.js', 
            'audio/afx.js'
        ];
        const bundledJs = filesToBundle.map(file => {
            const filePath = path.join(ayfxPath, file);
            if (!fs.existsSync(filePath)) {
                console.warn(`Missing file: ${filePath}`);
                return '';
            }
            
            // Strip module keywords to make files globally available
            return fs.readFileSync(filePath, 'utf8')
                .replace(/import .* from '.*';/g, '')
                .replace(/export default/g, '')
                .replace(/export (class|const|let|var|function)/g, '$1')
                .replace(/export const/g, 'const');
        }).join('\n\n');

        // Load frontend files
        const mainScript = fs.readFileSync(path.join(viewPath, 'main.js'), 'utf8');
        const style = fs.readFileSync(path.join(viewPath, 'style.css'), 'utf8');
        const htmlTemplate = fs.readFileSync(path.join(viewPath, 'index.html'), 'utf8');

        // Generate final HTML
        const finalHtml = htmlTemplate
            .replace('{{style}}', style)
            .replace('{{script}}', `${debounceFunction}\n\n${bundledJs}\n\n${mainScript}`);

        return finalHtml;
    }
} 