import * as vscode from 'vscode';
import { execFile, exec } from 'child_process';
import * as path from 'path';
import * as fs from 'fs';

export class PythonDetector {
    
    /**
     * Detect available Python installations on the system
     */
    public static async detectPythonInstallations(): Promise<string[]> {
        const pythonPaths: string[] = [];

        // Common Python executable names
        const pythonNames = process.platform === 'win32' 
            ? ['python.exe', 'python3.exe', 'py.exe']
            : ['python', 'python3', 'python3.11', 'python3.10', 'python3.9'];

        // Try system PATH first
        for (const pythonName of pythonNames) {
            try {
                const result = await this.testPythonPath(pythonName);
                if (result.isValid) {
                    pythonPaths.push(pythonName);
                }
            } catch (error) {
                // Ignore errors, continue with next candidate
            }
        }

        // Try common installation paths on Windows
        if (process.platform === 'win32') {
            const commonPaths = [
                'C:\\Python39\\python.exe',
                'C:\\Python310\\python.exe',
                'C:\\Python311\\python.exe',
                'C:\\Python312\\python.exe',
                'C:\\Users\\%USERNAME%\\AppData\\Local\\Programs\\Python\\Python39\\python.exe',
                'C:\\Users\\%USERNAME%\\AppData\\Local\\Programs\\Python\\Python310\\python.exe',
                'C:\\Users\\%USERNAME%\\AppData\\Local\\Programs\\Python\\Python311\\python.exe',
                'C:\\Users\\%USERNAME%\\AppData\\Local\\Programs\\Python\\Python312\\python.exe',
                'C:\\Program Files\\Python39\\python.exe',
                'C:\\Program Files\\Python310\\python.exe',
                'C:\\Program Files\\Python311\\python.exe',
                'C:\\Program Files\\Python312\\python.exe',
            ];

            for (const pythonPath of commonPaths) {
                const expandedPath = pythonPath.replace('%USERNAME%', process.env.USERNAME || 'USER');
                if (fs.existsSync(expandedPath)) {
                    try {
                        const result = await this.testPythonPath(expandedPath);
                        if (result.isValid && !pythonPaths.includes(expandedPath)) {
                            pythonPaths.push(expandedPath);
                        }
                    } catch (error) {
                        // Ignore errors
                    }
                }
            }
        }

        return pythonPaths;
    }

    /**
     * Test if a Python path is valid and get version info
     */
    public static testPythonPath(pythonPath: string): Promise<{isValid: boolean, version?: string, error?: string}> {
        return new Promise((resolve) => {
            execFile(pythonPath, ['--version'], (error, stdout, stderr) => {
                if (error) {
                    resolve({ isValid: false, error: error.message });
                    return;
                }

                const version = (stdout || stderr).trim();
                if (version.includes('Python')) {
                    resolve({ isValid: true, version });
                } else {
                    resolve({ isValid: false, error: 'Invalid Python version output' });
                }
            });
        });
    }

    /**
     * Get current Python path from configuration
     */
    public static getCurrentPythonPath(): string {
        const config = vscode.workspace.getConfiguration('nextbuild-viewers.linting');
        return config.get<string>('pythonPath', 'python');
    }

    /**
     * Set Python path in configuration
     */
    public static async setPythonPath(pythonPath: string): Promise<void> {
        const config = vscode.workspace.getConfiguration('nextbuild-viewers.linting');
        await config.update('pythonPath', pythonPath, vscode.ConfigurationTarget.Global);
    }

    /**
     * Show Python path selection dialog
     */
    public static async showPythonPathDialog(): Promise<void> {
        const currentPath = this.getCurrentPythonPath();
        
        // Test current path first
        vscode.window.withProgress({
            location: vscode.ProgressLocation.Notification,
            title: "Detecting Python installations...",
            cancellable: false
        }, async (progress) => {
            progress.report({ increment: 20, message: "Testing current Python path..." });
            
            const currentResult = await this.testPythonPath(currentPath);
            
            if (currentResult.isValid) {
                const useCurrentChoice = await vscode.window.showInformationMessage(
                    `Current Python path is working: ${currentPath} (${currentResult.version})`,
                    'Keep Current', 'Choose Different'
                );
                
                if (useCurrentChoice === 'Keep Current') {
                    return;
                }
            }

            progress.report({ increment: 50, message: "Scanning for Python installations..." });
            
            const detectedPaths = await this.detectPythonInstallations();
            
            progress.report({ increment: 90, message: "Preparing options..." });

            if (detectedPaths.length === 0) {
                vscode.window.showErrorMessage(
                    'No Python installations found. Please install Python and ensure it\'s in your system PATH, or manually set the path in settings.',
                    'Open Settings'
                ).then(selection => {
                    if (selection === 'Open Settings') {
                        vscode.commands.executeCommand('workbench.action.openSettings', 'nextbuild-viewers.linting.pythonPath');
                    }
                });
                return;
            }

            // Add options for manual selection
            const options = [
                ...detectedPaths.map(p => ({ label: p, description: 'Detected installation' })),
                { label: '$(edit) Manual Path...', description: 'Enter custom path' },
                { label: '$(gear) Open Settings', description: 'Configure in VS Code settings' }
            ];

            const selected = await vscode.window.showQuickPick(options, {
                placeHolder: 'Select Python executable for NextBuild linting',
                title: 'Python Path Configuration'
            });

            if (!selected) return;

            if (selected.label === '$(gear) Open Settings') {
                vscode.commands.executeCommand('workbench.action.openSettings', 'nextbuild-viewers.linting.pythonPath');
            } else if (selected.label === '$(edit) Manual Path...') {
                const manualPath = await vscode.window.showInputBox({
                    prompt: 'Enter full path to Python executable',
                    placeHolder: 'e.g., C:\\Python39\\python.exe or /usr/bin/python3',
                    value: currentPath
                });

                if (manualPath) {
                    const testResult = await this.testPythonPath(manualPath);
                    if (testResult.isValid) {
                        await this.setPythonPath(manualPath);
                        vscode.window.showInformationMessage(`Python path updated to: ${manualPath} (${testResult.version})`);
                    } else {
                        vscode.window.showErrorMessage(`Invalid Python path: ${testResult.error}`);
                    }
                }
            } else {
                // Selected a detected path
                const testResult = await this.testPythonPath(selected.label);
                if (testResult.isValid) {
                    await this.setPythonPath(selected.label);
                    vscode.window.showInformationMessage(`Python path updated to: ${selected.label} (${testResult.version})`);
                } else {
                    vscode.window.showErrorMessage(`Failed to verify Python path: ${testResult.error}`);
                }
            }
        });
    }

    /**
     * Show helpful error message with fix suggestions
     */
    public static showPythonNotFoundError(attemptedPath: string): void {
        const message = `Python executable not found at: ${attemptedPath}`;
        
        vscode.window.showErrorMessage(
            message,
            'Detect Python',
            'Use System Python', 
            'Open Settings'
        ).then(selection => {
            switch (selection) {
                case 'Detect Python':
                    this.showPythonPathDialog();
                    break;
                case 'Use System Python':
                    this.setPythonPath('python').then(() => {
                        vscode.window.showInformationMessage('Python path set to "python" (system PATH). Try linting again.');
                    });
                    break;
                case 'Open Settings':
                    vscode.commands.executeCommand('workbench.action.openSettings', 'nextbuild-viewers.linting.pythonPath');
                    break;
            }
        });
    }
} 