import * as vscode from 'vscode';
import * as fs from 'fs';
import * as path from 'path';

export interface PSGFrame {
    registers: number[]; // 14 registers (R0-R13)
}

export interface PSGData {
    frames: PSGFrame[];
    frameRate: number; // 50 or 60 Hz
    duration: number; // in seconds
}

export interface PSGImportOptions {
    sourceChannels: ('A' | 'B' | 'C')[];
    targetChannels: ('a' | 'b' | 'c')[];
    startFrame: number;
    endFrame: number;
    autoMap: boolean;
}

export interface PSGImportResult {
    options: PSGImportOptions;
    psgData: PSGData;
    filePath: string;
}

export class PSGImporter {
    constructor(private context: vscode.ExtensionContext) {}

    public static parsePSG(data: Buffer): PSGData | null {
        try {
            // Check PSG header
            if (data.length < 16) {
                throw new Error('File too small to be a valid PSG file');
            }

            // Check for PSG signature (first 3 bytes should be "PSG")
            const signature = data.subarray(0, 3);
            if (signature.toString('ascii') !== 'PSG') {
                throw new Error('Invalid PSG file signature');
            }

            // Skip header (16 bytes) and parse command stream
            let offset = 16;
            const frames: PSGFrame[] = [];
            let currentRegisters = new Array(16).fill(0); // AY has 16 registers (0-15)
            
            console.log('Parsing PSG command stream...');
            
            while (offset < data.length) {
                const command = data[offset];
                offset++;
                
                if (command >= 0x00 && command <= 0x0F) {
                    // Register write command: next byte is data for this register
                    if (offset >= data.length) {
                        console.warn('Incomplete register write command at end of file');
                        break;
                    }
                    const value = data[offset];
                    offset++;
                    currentRegisters[command] = value;
                    console.log(`Register R${command.toString(16).toUpperCase()} = 0x${value.toString(16).toUpperCase()}`);
                    
                } else if (command === 0xFF) {
                    // Wait 1 frame - capture current register state
                    frames.push({
                        registers: [...currentRegisters] // Make a copy
                    });
                    console.log(`Frame ${frames.length}: captured register state`);
                    
                } else if (command === 0xFE) {
                    // Wait multiple frames
                    if (offset >= data.length) {
                        console.warn('Incomplete multiple wait command at end of file');
                        break;
                    }
                    const waitCount = data[offset];
                    offset++;
                    
                    // Add multiple frames with same register state
                    for (let i = 0; i < waitCount; i++) {
                        frames.push({
                            registers: [...currentRegisters] // Make a copy
                        });
                    }
                    console.log(`Added ${waitCount} wait frames`);
                    
                } else if (command === 0xFD) {
                    // End of music
                    console.log('End of music command found');
                    break;
                    
                } else {
                    console.warn(`Unknown PSG command: 0x${command.toString(16).toUpperCase()} at offset ${offset - 1}`);
                }
            }

            if (frames.length === 0) {
                throw new Error('No frames captured from PSG file');
            }

            // Assume 50Hz (PAL) by default
            const frameRate = 50;
            const duration = frames.length / frameRate;

            console.log(`PSG parsing complete: ${frames.length} frames, ${duration.toFixed(2)}s duration`);

            return {
                frames,
                frameRate,
                duration
            };
        } catch (error) {
            console.error('Failed to parse PSG file:', error);
            return null;
        }
    }

    public static convertPSGToAYFX(psgData: PSGData, options: PSGImportOptions): { channelData: { [key: string]: any[] }, effectName: string } {
        const { sourceChannels, targetChannels, startFrame, endFrame } = options;
        const channelData: { [key: string]: any[] } = {};

        console.log(`PSG Convert - Source channels: ${sourceChannels}, Target channels: ${targetChannels}`);

        // Use the selected channels or defaults
        const actualSourceChannels = sourceChannels.length > 0 ? sourceChannels : ['A'];
        const actualTargetChannels = targetChannels.length > 0 ? targetChannels : ['a'];
        
        // Respect user's channel selection - don't force channel A
        const finalSourceChannels: ('A' | 'B' | 'C')[] = actualSourceChannels as ('A' | 'B' | 'C')[];
        const finalTargetChannels: ('a' | 'b' | 'c')[] = actualTargetChannels as ('a' | 'b' | 'c')[];

        console.log(`PSG Convert - Final mapping: ${finalSourceChannels}  ${finalTargetChannels}`);

        // Initialize target channels
        finalTargetChannels.forEach(channel => {
            channelData[channel] = [];
        });

        // Process frames in the specified range
        const actualStartFrame = Math.max(0, startFrame);
        const actualEndFrame = Math.min(psgData.frames.length - 1, endFrame);

        console.log(`PSG Convert - Processing frames ${actualStartFrame} to ${actualEndFrame}`);

        for (let frameIndex = actualStartFrame; frameIndex <= actualEndFrame; frameIndex++) {
            const frame = psgData.frames[frameIndex];
            
            // Map source channels to target channels
            for (let i = 0; i < Math.min(finalSourceChannels.length, finalTargetChannels.length); i++) {
                const sourceChannel = finalSourceChannels[i];
                const targetChannel = finalTargetChannels[i];
                
                // Extract AY register data for this channel
                const ayFrame = PSGImporter.extractAYChannelData(frame, sourceChannel);
                channelData[targetChannel].push(ayFrame);
            }
        }

        const effectName = `PSG_Import_${actualStartFrame}-${actualEndFrame}`;
        console.log(`PSG Convert - Complete. Channel data:`, Object.keys(channelData));
        return { channelData, effectName };
    }

    private static extractAYChannelData(frame: PSGFrame, channel: 'A' | 'B' | 'C'): any {
        const registers = frame.registers;
        
        // AY-3-8910 register mapping
        let toneRegLow, toneRegHigh, volumeReg;
        
        switch (channel) {
            case 'A':
                toneRegLow = 0;   // R0
                toneRegHigh = 1;  // R1
                volumeReg = 8;    // R8
                break;
            case 'B':
                toneRegLow = 2;   // R2
                toneRegHigh = 3;  // R3
                volumeReg = 9;    // R9
                break;
            case 'C':
                toneRegLow = 4;   // R4
                toneRegHigh = 5;  // R5
                volumeReg = 10;   // R10
                break;
        }

        // Extract tone period (12-bit value)
        const toneLow = registers[toneRegLow] || 0;
        const toneHigh = (registers[toneRegHigh] || 0) & 0x0F;
        const tone = toneLow | (toneHigh << 8);

        // Extract volume (4-bit value)
        const volume = (registers[volumeReg] || 0) & 0x0F;

        // Extract noise and mixer settings
        const noiseReg = (registers[6] || 0) & 0x1F; // R6 - Noise period
        const mixerReg = registers[7] || 0; // R7 - Mixer

        // Determine if tone/noise are enabled for this channel
        const channelBit = channel === 'A' ? 0 : channel === 'B' ? 1 : 2;
        const toneEnabled = (mixerReg & (1 << channelBit)) === 0; // 0 = enabled
        const noiseEnabled = (mixerReg & (1 << (channelBit + 3))) === 0; // 0 = enabled

        return {
            t: toneEnabled && tone > 0,
            n: noiseEnabled,
            tone: tone,
            noise: noiseReg,
            volume: volume
        };
    }

    private selectedPSGData: PSGData | null = null;
    private selectedFilePath: string | null = null;

    public async showImportDialog(): Promise<PSGImportResult | null> {
        return new Promise((resolve) => {
            const panel = vscode.window.createWebviewPanel(
                'psgImport',
                'Import PSG File',
                vscode.ViewColumn.One,
                {
                    enableScripts: true,
                    localResourceRoots: [vscode.Uri.file(path.join(this.context.extensionPath, 'src'))]
                }
            );

            panel.webview.html = this.getImportDialogHTML();

            panel.webview.onDidReceiveMessage(
                message => {
                    switch (message.command) {
                        case 'selectFile':
                            this.handleFileSelection(panel);
                            break;
                        case 'import':
                            if (this.selectedPSGData && this.selectedFilePath) {
                                resolve({
                                    options: message.options,
                                    psgData: this.selectedPSGData,
                                    filePath: this.selectedFilePath
                                });
                            } else {
                                resolve(null);
                            }
                            panel.dispose();
                            break;
                        case 'cancel':
                            resolve(null);
                            panel.dispose();
                            break;
                    }
                },
                undefined,
                this.context.subscriptions
            );

            panel.onDidDispose(() => {
                resolve(null);
            });
        });
    }

    private async handleFileSelection(panel: vscode.WebviewPanel) {
        const fileUri = await vscode.window.showOpenDialog({
            canSelectFiles: true,
            canSelectFolders: false,
            canSelectMany: false,
            filters: {
                'PSG Files': ['psg'],
                'All Files': ['*']
            }
        });

        if (fileUri && fileUri[0]) {
            const filePath = fileUri[0].fsPath;
            
            // Show processing message
            panel.webview.postMessage({
                command: 'processingStarted'
            });
            
            try {
                const data = fs.readFileSync(filePath);
                const psgData = PSGImporter.parsePSG(data);

                if (psgData) {
                    // Store the parsed data for later use
                    this.selectedPSGData = psgData;
                    this.selectedFilePath = filePath;
                    
                    // For now, we'll skip audio preview generation since we need access to Effect class
                    // In a future update, we could implement a simple AYFX preview generator
                    
                    panel.webview.postMessage({
                        command: 'fileSelected',
                        filename: path.basename(filePath),
                        frameCount: psgData.frames.length,
                        duration: psgData.duration,
                        frameRate: psgData.frameRate,
                        hasPreview: true // Show preview section even without audio data
                    });
                } else {
                    vscode.window.showErrorMessage('Failed to parse PSG file');
                }
            } catch (error) {
                vscode.window.showErrorMessage(`Error reading PSG file: ${error}`);
            }
        }
    }

    private getImportDialogHTML(): string {
        return `<!DOCTYPE html>
<html lang="en">
<head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <title>Import PSG File</title>
    <style>
        body {
            font-family: var(--vscode-font-family);
            font-size: var(--vscode-font-size);
            color: var(--vscode-foreground);
            background-color: var(--vscode-editor-background);
            padding: 20px;
        }
        .container {
            max-width: 600px;
            margin: 0 auto;
        }
        .section {
            margin-bottom: 24px;
            padding: 16px;
            border: 1px solid var(--vscode-widget-border);
            border-radius: 4px;
        }
        .section h3 {
            margin-top: 0;
            color: var(--vscode-titleBar-activeForeground);
        }
        .file-info {
            background-color: var(--vscode-textBlockQuote-background);
            padding: 12px;
            border-radius: 4px;
            margin-bottom: 16px;
        }
        .form-row {
            display: flex;
            align-items: center;
            margin-bottom: 12px;
        }
        .form-row label {
            min-width: 120px;
            margin-right: 12px;
        }
        select, input[type="number"] {
            background-color: var(--vscode-input-background);
            color: var(--vscode-input-foreground);
            border: 1px solid var(--vscode-input-border);
            padding: 4px 8px;
            border-radius: 2px;
        }
        .channel-mapping {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 16px;
        }
        .channel-group {
            border: 1px solid var(--vscode-widget-border);
            padding: 12px;
            border-radius: 4px;
        }
        .channel-group h4 {
            margin-top: 0;
            margin-bottom: 8px;
        }
        .checkbox-group {
            display: flex;
            flex-direction: column;
            gap: 4px;
        }
        .range-inputs {
            display: flex;
            gap: 8px;
            align-items: center;
        }
        .range-inputs input {
            width: 80px;
        }
        .buttons {
            display: flex;
            gap: 12px;
            justify-content: flex-end;
            margin-top: 24px;
        }
        button {
            background-color: var(--vscode-button-background);
            color: var(--vscode-button-foreground);
            border: none;
            padding: 8px 16px;
            border-radius: 2px;
            cursor: pointer;
        }
        button:hover {
            background-color: var(--vscode-button-hoverBackground);
        }
        button.secondary {
            background-color: var(--vscode-button-secondaryBackground);
            color: var(--vscode-button-secondaryForeground);
        }
        button.secondary:hover {
            background-color: var(--vscode-button-secondaryHoverBackground);
        }
        button:disabled {
            opacity: 0.5;
            cursor: not-allowed;
        }
        .auto-option {
            background-color: var(--vscode-textBlockQuote-background);
            padding: 12px;
            border-radius: 4px;
            margin-bottom: 16px;
        }
    </style>
</head>
<body>
    <div class="container">
        <h2>Import PSG File to AYFX</h2>
        
        <div class="section">
            <h3>1. Select PSG File</h3>
            <button id="selectFileBtn">Select PSG File...</button>
            <div id="fileInfo" class="file-info" style="display: none;">
                <div><strong>File:</strong> <span id="fileName">-</span></div>
                <div><strong>Frames:</strong> <span id="frameCount">-</span></div>
                <div><strong>Duration:</strong> <span id="duration">-</span> seconds</div>
                <div><strong>Frame Rate:</strong> <span id="frameRate">-</span> Hz</div>
            </div>
        </div>

        <div class="section" id="audioPreview" style="display: none;">
            <h3>2. Audio Preview</h3>
            <div id="previewControls">
                <button id="playPreviewBtn" class="secondary"> Play Preview</button>
                <button id="stopPreviewBtn" class="secondary" disabled> Stop</button>
                <span id="previewInfo" style="margin-left: 12px; color: var(--vscode-descriptionForeground);"></span>
            </div>
        </div>

        <div class="section" id="importOptions" style="display: none;">
            <h3>3. Import Options</h3>
            
                         <div class="auto-option">
                 <label>
                     <input type="checkbox" id="autoMap">
                     <strong>Auto-map channels</strong> (map PSG channels Aa, Bb, Cc)
                 </label>
                 <div style="margin-top: 8px; font-size: 0.9em; color: var(--vscode-descriptionForeground);">
                     <strong>Manual mode:</strong> Select specific channels to import below
                 </div>
             </div>

            <div id="manualMapping" style="display: block;">
                <h4>Channel Mapping</h4>
                <div class="channel-mapping">
                    <div class="channel-group">
                        <h4>PSG Source Channels</h4>
                        <div class="checkbox-group">
                            <label><input type="checkbox" name="sourceChannel" value="A" checked> Channel A (Tone 1)</label>
                            <label><input type="checkbox" name="sourceChannel" value="B"> Channel B (Tone 2)</label>
                            <label><input type="checkbox" name="sourceChannel" value="C"> Channel C (Tone 3)</label>
                        </div>
                    </div>
                    <div class="channel-group">
                        <h4>AYFX Target Channels</h4>
                        <div class="checkbox-group">
                            <label><input type="checkbox" name="targetChannel" value="a" checked> Channel A</label>
                            <label><input type="checkbox" name="targetChannel" value="b"> Channel B</label>
                            <label><input type="checkbox" name="targetChannel" value="c"> Channel C</label>
                        </div>
                    </div>
                </div>
            </div>

            <h4>Frame Range</h4>
            <div class="form-row">
                <label>Import frames:</label>
                <div class="range-inputs">
                    <input type="number" id="startFrame" value="0" min="0">
                    <span>to</span>
                    <input type="number" id="endFrame" value="0" min="0">
                    <button id="selectAllFrames" class="secondary">Select All</button>
                </div>
            </div>
        </div>

        <div class="buttons">
            <button id="cancelBtn" class="secondary">Cancel</button>
            <button id="importBtn" disabled>Import</button>
        </div>
    </div>

    <script>
        const vscode = acquireVsCodeApi();
        
        let psgData = null;
        
        // DOM elements
        const selectFileBtn = document.getElementById('selectFileBtn');
        const fileInfo = document.getElementById('fileInfo');
        const fileName = document.getElementById('fileName');
        const frameCount = document.getElementById('frameCount');
        const duration = document.getElementById('duration');
        const frameRate = document.getElementById('frameRate');
        const importOptions = document.getElementById('importOptions');
        const autoMapCheckbox = document.getElementById('autoMap');
        const manualMapping = document.getElementById('manualMapping');
        const startFrameInput = document.getElementById('startFrame');
        const endFrameInput = document.getElementById('endFrame');
        const selectAllFramesBtn = document.getElementById('selectAllFrames');
        const importBtn = document.getElementById('importBtn');
        const cancelBtn = document.getElementById('cancelBtn');
        const audioPreview = document.getElementById('audioPreview');
        const playPreviewBtn = document.getElementById('playPreviewBtn');
        const stopPreviewBtn = document.getElementById('stopPreviewBtn');
        const previewInfo = document.getElementById('previewInfo');
        
        let audioContext = null;
        let audioSource = null;

        // Event listeners
        selectFileBtn.addEventListener('click', () => {
            vscode.postMessage({ command: 'selectFile' });
        });

        autoMapCheckbox.addEventListener('change', () => {
            manualMapping.style.display = autoMapCheckbox.checked ? 'none' : 'block';
        });

        selectAllFramesBtn.addEventListener('click', () => {
            if (psgData) {
                startFrameInput.value = '0';
                endFrameInput.value = (psgData.frameCount - 1).toString();
            }
        });

                 importBtn.addEventListener('click', () => {
             let sourceChannels, targetChannels;
             
             if (autoMapCheckbox.checked) {
                 // Auto-map mode: map all channels
                 sourceChannels = ['A', 'B', 'C'];
                 targetChannels = ['a', 'b', 'c'];
             } else {
                 // Manual mode: get selected channels from checkboxes
                 sourceChannels = Array.from(document.querySelectorAll('input[name="sourceChannel"]:checked'))
                     .map(input => input.value);
                 targetChannels = Array.from(document.querySelectorAll('input[name="targetChannel"]:checked'))
                     .map(input => input.value);
                 
                 // Fallback to defaults if nothing selected
                 if (sourceChannels.length === 0) sourceChannels = ['A'];
                 if (targetChannels.length === 0) targetChannels = ['a'];
             }
             
             const options = {
                 sourceChannels,
                 targetChannels,
                 startFrame: parseInt(startFrameInput.value) || 0,
                 endFrame: parseInt(endFrameInput.value) || 0,
                 autoMap: autoMapCheckbox.checked
             };
             
             console.log('PSG Import options:', options);
             vscode.postMessage({ command: 'import', options });
         });

        cancelBtn.addEventListener('click', () => {
            vscode.postMessage({ command: 'cancel' });
        });

        playPreviewBtn.addEventListener('click', () => {
            playAudioPreview();
        });

        stopPreviewBtn.addEventListener('click', () => {
            stopAudioPreview();
        });

        function addAudioPreview() {
            audioPreview.style.display = 'block';
            previewInfo.textContent = 'Import to hear audio preview in the main editor';
            
            // Disable preview buttons since we don't have audio data yet
            playPreviewBtn.disabled = true;
            playPreviewBtn.textContent = ' Preview after import';
        }

        function playAudioPreview() {
            if (!window.previewAudioData) return;
            
            try {
                audioContext = audioContext || new (window.AudioContext || window.webkitAudioContext)();
                
                // Decode the audio data
                audioContext.decodeAudioData(window.previewAudioData.slice(0), (buffer) => {
                    audioSource = audioContext.createBufferSource();
                    audioSource.buffer = buffer;
                    
                    const gainNode = audioContext.createGain();
                    gainNode.gain.value = 0.5; // Lower volume for preview
                    
                    audioSource.connect(gainNode);
                    gainNode.connect(audioContext.destination);
                    
                    audioSource.onended = () => {
                        stopPreviewBtn.disabled = true;
                        playPreviewBtn.disabled = false;
                        playPreviewBtn.textContent = ' Play Preview';
                    };
                    
                    audioSource.start(0);
                    
                    playPreviewBtn.disabled = true;
                    stopPreviewBtn.disabled = false;
                    playPreviewBtn.textContent = ' Playing...';
                    
                }, (error) => {
                    console.error('Audio decode error:', error);
                    previewInfo.textContent = 'Error playing preview';
                });
                
            } catch (error) {
                console.error('Audio preview error:', error);
                previewInfo.textContent = 'Audio preview not available';
            }
        }

        function stopAudioPreview() {
            if (audioSource) {
                audioSource.stop();
                audioSource = null;
            }
            stopPreviewBtn.disabled = true;
            playPreviewBtn.disabled = false;
            playPreviewBtn.textContent = ' Play Preview';
        }

        // Handle messages from extension
        window.addEventListener('message', event => {
            const message = event.data;
            
            switch (message.command) {
                case 'processingStarted':
                    // Show processing message
                    fileInfo.style.display = 'block';
                    fileName.textContent = 'Processing...';
                    frameCount.textContent = '...';
                    duration.textContent = '...';
                    frameRate.textContent = '...';
                    importOptions.style.display = 'none';
                    importBtn.disabled = true;
                    break;
                    
                case 'fileSelected':
                    psgData = message;
                    fileName.textContent = message.filename;
                    frameCount.textContent = message.frameCount;
                    duration.textContent = message.duration.toFixed(2);
                    frameRate.textContent = message.frameRate;
                    
                    endFrameInput.value = (message.frameCount - 1).toString();
                    endFrameInput.max = (message.frameCount - 1).toString();
                    startFrameInput.max = (message.frameCount - 1).toString();
                    
                    fileInfo.style.display = 'block';
                    importOptions.style.display = 'block';
                    importBtn.disabled = false;
                    
                    // Add preview section
                    if (message.hasPreview) {
                        addAudioPreview();
                    }
                    break;
            }
        });
    </script>
</body>
</html>`;
    }
} 