import * as vscode from 'vscode';

/**
 * Provides systematic CodeActions for NextBuild language files based on error patterns.
 */
export class NextBuildCodeActionProvider implements vscode.CodeActionProvider {

    public static readonly providedCodeActionKinds = [
        vscode.CodeActionKind.QuickFix
    ];

    // Error pattern definitions based on implementation guide
    private readonly ERROR_PATTERNS = {
        // Syntax errors
        generalSyntaxError: /Syntax Error\. Unexpected token '([^']+)' </,
        unrecognizedKeyword: /Unrecognised (?:instruction|keyword|identifier): (\w+)/i,
        
        // Type and declaration errors
        implicitType: /\[W100\] Using default implicit type '(\w+)' for '([^']+)'/i,
        undeclaredType: /missing type declaration for '([^']+)'/i,
        functionVsSub: /'([^']+)' is a (SUB|FUNCTION) not a (FUNCTION|SUB)/i,
        cannotAssign: /Cannot assign a value to '([^']+)'\. It's not a variable/i,
        
        // Array and function errors
        neitherArrayNorFunction: /'([^']+)' is neither an array nor a function/i,
        arrayDimensions: /Arrays '([^']+)' and '([^']+)' don't have the same number of dimensions/i,
        
        // FOR loop errors
        forVariable: /FOR variable should be '([^']+)' instead of '([^']+)'/i,
        
        // Function warnings
        valWarning: /Invalid string numeric constant '([^']+)' evaluated as 0/i,
        redundantAbs: /Redundant operation ABS/i,
        signUnsigned: /Sign of unsigned value is always 0 or 1/i,
        stepWarning: /STEP value is 0 and FOR might loop forever/i,
        
        // Structure errors
        missingEndif: /Missing ENDIF/i,
        missingNext: /Missing NEXT/i,
        forWithoutNext: /FOR without NEXT/i,
        doNotClosed: /DO loop not closed/i,
        
        // Preprocessor errors
        illegalPreprocessor: /illegal preprocessor character '([^']+)'/i,
    };

    // Common typos dictionary (can be expanded)
    private readonly COMMON_TYPOS: Record<string, string> = {
        'LOOAD': 'LOAD',
        'PRUNT': 'PRINT',
        'IPUT': 'INPUT',
        'FOOR': 'FOR',
        'NEXTT': 'NEXT',
        'WHILEE': 'WHILE',
        'REPEAAT': 'REPEAT',
        'LETT': 'LET',
        'THENN': 'THEN',
        'ELSEE': 'ELSE',
        'AAN': 'AND',
        'ORR': 'OR',
        'NOOT': 'NOT',
        'DIIM': 'DIM',
        'FUNCTOIN': 'FUNCTION',
        'SUUB': 'SUB',
        'RETRUN': 'RETURN',
        'PRIINT': 'PRINT',
    };

    provideCodeActions(document: vscode.TextDocument, range: vscode.Range | vscode.Selection, 
                      context: vscode.CodeActionContext, token: vscode.CancellationToken): vscode.ProviderResult<(vscode.Command | vscode.CodeAction)[]> {
        const actions: vscode.CodeAction[] = [];

        // Process each diagnostic
        for (const diagnostic of context.diagnostics) {
            if (diagnostic.source === 'NextBuild') {
                // Handle different error categories systematically
                actions.push(...this.handleSyntaxErrors(diagnostic, document));
                actions.push(...this.handleTypeErrors(diagnostic, document));
                actions.push(...this.handleArrayAndFunctionErrors(diagnostic, document));
                actions.push(...this.handleForLoopErrors(diagnostic, document));
                actions.push(...this.handleFunctionWarnings(diagnostic, document));
                actions.push(...this.handleStructureErrors(diagnostic, document));
                actions.push(...this.handlePreprocessorErrors(diagnostic, document));
            }
        }

        return actions;
    }

    private handleSyntaxErrors(diagnostic: vscode.Diagnostic, document: vscode.TextDocument): vscode.CodeAction[] {
        const actions: vscode.CodeAction[] = [];
        const message = diagnostic.message;

        // Handle unrecognized keywords with typo correction
        const unrecognizedMatch = message.match(this.ERROR_PATTERNS.unrecognizedKeyword);
        if (unrecognizedMatch && unrecognizedMatch[1]) {
            const incorrectWord = unrecognizedMatch[1];
            const upperWord = incorrectWord.toUpperCase();
            
            const correction = this.COMMON_TYPOS[upperWord];
            if (correction) {
                const fix = new vscode.CodeAction(`Change '${incorrectWord}' to '${correction}'`, vscode.CodeActionKind.QuickFix);
                fix.edit = new vscode.WorkspaceEdit();
                fix.edit.replace(document.uri, diagnostic.range, correction);
                fix.diagnostics = [diagnostic];
                fix.isPreferred = true;
                actions.push(fix);
            }
        }

        // Handle general syntax errors (extract token and suggest alternatives)
        const syntaxMatch = message.match(this.ERROR_PATTERNS.generalSyntaxError);
        if (syntaxMatch && syntaxMatch[1]) {
            const unexpectedToken = syntaxMatch[1];
            
            // Check if it's a typo
            const correction = this.COMMON_TYPOS[unexpectedToken.toUpperCase()];
            if (correction) {
                const fix = new vscode.CodeAction(`Did you mean '${correction}'?`, vscode.CodeActionKind.QuickFix);
                fix.edit = new vscode.WorkspaceEdit();
                fix.edit.replace(document.uri, diagnostic.range, correction);
                fix.diagnostics = [diagnostic];
                actions.push(fix);
            }
        }

        return actions;
    }

    private handleTypeErrors(diagnostic: vscode.Diagnostic, document: vscode.TextDocument): vscode.CodeAction[] {
        const actions: vscode.CodeAction[] = [];
        const message = diagnostic.message;

        // Handle implicit type warnings
        const implicitTypeMatch = message.match(this.ERROR_PATTERNS.implicitType);
        if (implicitTypeMatch && implicitTypeMatch[1] && implicitTypeMatch[2]) {
            const type = implicitTypeMatch[1];
            const variable = implicitTypeMatch[2];
            
            // For ZX Spectrum Next development, suggest uByte instead of float as it's more practical
            const suggestedType = type.toLowerCase() === 'float' ? 'uByte' : type;
            
            const fix = new vscode.CodeAction(`Add explicit '${suggestedType}' type to '${variable}'`, vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            // Find the line with the variable declaration and add type
            for (let i = 0; i < document.lineCount; i++) {
                const line = document.lineAt(i);
                if (line.text.includes(variable) && 
                    (line.text.includes('DIM') || line.text.includes('LET') || 
                     line.text.match(new RegExp(`\\b${variable}\\s*=`, 'i')))) {
                    let newText: string;
                    if (line.text.includes('DIM')) {
                        // DIM variable AS type
                        newText = line.text.replace(
                            new RegExp(`\\b${variable}\\b`, 'i'), 
                            `${variable} AS ${suggestedType}`
                        );
                    } else {
                        // Variable assignment - add DIM statement above
                        const indentation = line.text.match(/^\s*/)?.[0] || '';
                        fix.edit.insert(document.uri, new vscode.Position(i, 0), 
                            `${indentation}DIM ${variable} AS ${suggestedType}\n`);
                        break;
                    }
                    
                    if (newText) {
                        fix.edit.replace(document.uri, line.range, newText);
                    }
                    break;
                }
            }
            
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        // Handle undeclared type errors
        const undeclaredMatch = message.match(this.ERROR_PATTERNS.undeclaredType);
        if (undeclaredMatch && undeclaredMatch[1]) {
            const variable = undeclaredMatch[1];
            
            // Suggest common types (prioritize uByte for ZX Spectrum Next)
            const commonTypes = ['uByte', 'uInteger', 'STRING', 'FLOAT'];
            commonTypes.forEach((type: any) => {
                const fix = new vscode.CodeAction(`Declare '${variable}' as ${type}`, vscode.CodeActionKind.QuickFix);
                fix.edit = new vscode.WorkspaceEdit();
                fix.edit.insert(document.uri, new vscode.Position(0, 0), 
                    `DIM ${variable} AS ${type}\n`);
                fix.diagnostics = [diagnostic];
                actions.push(fix);
            });
        }

        // Handle function vs SUB errors
        const funcSubMatch = message.match(this.ERROR_PATTERNS.functionVsSub);
        if (funcSubMatch && funcSubMatch[1] && funcSubMatch[2] && funcSubMatch[3]) {
            const name = funcSubMatch[1];
            const currentType = funcSubMatch[2];
            const expectedType = funcSubMatch[3];
            
            const fix = new vscode.CodeAction(`Change ${currentType} to ${expectedType}`, vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            // Find and replace the declaration
            for (let i = 0; i < document.lineCount; i++) {
                const line = document.lineAt(i);
                if (line.text.includes(currentType) && line.text.includes(name)) {
                    const newText = line.text.replace(currentType, expectedType);
                    fix.edit.replace(document.uri, line.range, newText);
                    break;
                }
            }
            
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        return actions;
    }

    private handleArrayAndFunctionErrors(diagnostic: vscode.Diagnostic, document: vscode.TextDocument): vscode.CodeAction[] {
        const actions: vscode.CodeAction[] = [];
        const message = diagnostic.message;

        // Handle "neither array nor function" errors
        const neitherMatch = message.match(this.ERROR_PATTERNS.neitherArrayNorFunction);
        if (neitherMatch && neitherMatch[1]) {
            const name = neitherMatch[1];
            
            // Suggest declaring as function
            const funcFix = new vscode.CodeAction(`Declare '${name}' as function`, vscode.CodeActionKind.QuickFix);
            funcFix.edit = new vscode.WorkspaceEdit();
            funcFix.edit.insert(document.uri, new vscode.Position(0, 0), 
                `FUNCTION ${name}()\n    ' Add your function code here\n    RETURN 0\nEND FUNCTION\n\n`);
            funcFix.diagnostics = [diagnostic];
            actions.push(funcFix);
            
            // Suggest declaring as array
            const arrayFix = new vscode.CodeAction(`Declare '${name}' as array`, vscode.CodeActionKind.QuickFix);
            arrayFix.edit = new vscode.WorkspaceEdit();
            arrayFix.edit.insert(document.uri, new vscode.Position(0, 0), 
                `DIM ${name}(10) AS INTEGER ' Adjust size and type as needed\n\n`);
            arrayFix.diagnostics = [diagnostic];
            actions.push(arrayFix);
        }

        // Handle array dimension mismatch
        const arrayDimMatch = message.match(this.ERROR_PATTERNS.arrayDimensions);
        if (arrayDimMatch && arrayDimMatch[1] && arrayDimMatch[2]) {
            const array1 = arrayDimMatch[1];
            const array2 = arrayDimMatch[2];
            
            const fix = new vscode.CodeAction(`Check array dimensions for '${array1}' and '${array2}'`, vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            // Add a comment to highlight the issue
            const lineNumber = diagnostic.range.start.line;
            const lineText = document.lineAt(lineNumber).text;
            const indentation = lineText.match(/^\s*/)?.[0] || '';
            
            fix.edit.insert(document.uri, new vscode.Position(lineNumber, 0), 
                `${indentation}' TODO: Ensure ${array1} and ${array2} have the same dimensions\n`);
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        return actions;
    }

    private handleForLoopErrors(diagnostic: vscode.Diagnostic, document: vscode.TextDocument): vscode.CodeAction[] {
        const actions: vscode.CodeAction[] = [];
        const message = diagnostic.message;

        // Handle FOR variable mismatch
        const forVarMatch = message.match(this.ERROR_PATTERNS.forVariable);
        if (forVarMatch && forVarMatch[1] && forVarMatch[2]) {
            const correctVar = forVarMatch[1];
            const wrongVar = forVarMatch[2];
            
            const fix = new vscode.CodeAction(`Change '${wrongVar}' to '${correctVar}'`, vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            fix.edit.replace(document.uri, diagnostic.range, correctVar);
            fix.diagnostics = [diagnostic];
            fix.isPreferred = true;
            actions.push(fix);
        }

        return actions;
    }

    private handleFunctionWarnings(diagnostic: vscode.Diagnostic, document: vscode.TextDocument): vscode.CodeAction[] {
        const actions: vscode.CodeAction[] = [];
        const message = diagnostic.message;

        // Handle VAL function warnings
        const valMatch = message.match(this.ERROR_PATTERNS.valWarning);
        if (valMatch && valMatch[1]) {
            const invalidConstant = valMatch[1];
            
            const fix = new vscode.CodeAction(`Remove invalid string constant '${invalidConstant}'`, vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            // Find VAL function call and suggest replacement
            const lineNumber = diagnostic.range.start.line;
            const line = document.lineAt(lineNumber);
            const newText = line.text.replace(new RegExp(`VAL\\s*\\(\\s*"${invalidConstant.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}"\\s*\\)`, 'i'), '0');
            
            fix.edit.replace(document.uri, line.range, newText);
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        // Handle redundant ABS
        if (message.match(this.ERROR_PATTERNS.redundantAbs)) {
            const fix = new vscode.CodeAction('Remove redundant ABS function', vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            const lineNumber = diagnostic.range.start.line;
            const line = document.lineAt(lineNumber);
            // Remove ABS() wrapper
            const newText = line.text.replace(/\bABS\s*\(\s*([^)]+)\s*\)/i, '$1');
            
            fix.edit.replace(document.uri, line.range, newText);
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        // Handle SGN with unsigned values
        if (message.match(this.ERROR_PATTERNS.signUnsigned)) {
            const fix = new vscode.CodeAction('Replace SGN with direct comparison', vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            const lineNumber = diagnostic.range.start.line;
            const line = document.lineAt(lineNumber);
            // Replace SGN(x) with (x > 0)
            const newText = line.text.replace(/\bSGN\s*\(\s*([^)]+)\s*\)/i, '($1 > 0)');
            
            fix.edit.replace(document.uri, line.range, newText);
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        // Handle STEP warnings
        if (message.match(this.ERROR_PATTERNS.stepWarning)) {
            const fix = new vscode.CodeAction('Change STEP value to non-zero', vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            const lineNumber = diagnostic.range.start.line;
            const line = document.lineAt(lineNumber);
            // Replace STEP 0 with STEP 1
            const newText = line.text.replace(/\bSTEP\s+0\b/i, 'STEP 1');
            
            fix.edit.replace(document.uri, line.range, newText);
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        return actions;
    }

    private handleStructureErrors(diagnostic: vscode.Diagnostic, document: vscode.TextDocument): vscode.CodeAction[] {
        const actions: vscode.CodeAction[] = [];
        const message = diagnostic.message;

        // Handle missing ENDIF
        if (message.match(this.ERROR_PATTERNS.missingEndif)) {
            const fix = new vscode.CodeAction('Add missing ENDIF', vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            const lineNumber = diagnostic.range.start.line;
            const lineText = document.lineAt(lineNumber).text;
            const indentation = lineText.match(/^\s*/)?.[0] || '';
            
            fix.edit.insert(document.uri, new vscode.Position(lineNumber + 1, 0), `${indentation}ENDIF\n`);
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        // Handle missing NEXT
        if (message.match(this.ERROR_PATTERNS.missingNext)) {
            const fix = new vscode.CodeAction('Add missing NEXT', vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            const lineNumber = diagnostic.range.start.line;
            const lineText = document.lineAt(lineNumber).text;
            const indentation = lineText.match(/^\s*/)?.[0] || '';
            
            fix.edit.insert(document.uri, new vscode.Position(lineNumber + 1, 0), `${indentation}NEXT\n`);
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        // Handle FOR without NEXT
        if (message.match(this.ERROR_PATTERNS.forWithoutNext)) {
            const fix = new vscode.CodeAction('Add missing NEXT for FOR loop', vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            // Find the end of the file or next major structure and add NEXT
            const lineNumber = diagnostic.range.start.line;
            let insertLine = lineNumber + 1;
            
            // Look for a good place to insert NEXT
            for (let i = lineNumber + 1; i < document.lineCount; i++) {
                const line = document.lineAt(i);
                if (line.text.trim() === '' || 
                    line.text.includes('FOR') || 
                    line.text.includes('IF') || 
                    line.text.includes('WHILE')) {
                    insertLine = i;
                    break;
                }
                insertLine = i + 1;
            }
            
            const baseIndentation = document.lineAt(lineNumber).text.match(/^\s*/)?.[0] || '';
            fix.edit.insert(document.uri, new vscode.Position(insertLine, 0), `${baseIndentation}NEXT\n`);
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        // Handle DO loop not closed
        if (message.match(this.ERROR_PATTERNS.doNotClosed)) {
            const fix = new vscode.CodeAction('Add missing LOOP for DO statement', vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            
            const lineNumber = diagnostic.range.start.line;
            const baseIndentation = document.lineAt(lineNumber).text.match(/^\s*/)?.[0] || '';
            
            // Add LOOP at the end of the apparent block
            let insertLine = lineNumber + 1;
            for (let i = lineNumber + 1; i < document.lineCount; i++) {
                const line = document.lineAt(i);
                if (line.text.trim() === '' || 
                    line.text.includes('DO') || 
                    line.text.includes('FOR') || 
                    line.text.includes('IF')) {
                    insertLine = i;
                    break;
                }
                insertLine = i + 1;
            }
            
            fix.edit.insert(document.uri, new vscode.Position(insertLine, 0), `${baseIndentation}LOOP\n`);
            fix.diagnostics = [diagnostic];
            actions.push(fix);
        }

        return actions;
    }

    private handlePreprocessorErrors(diagnostic: vscode.Diagnostic, document: vscode.TextDocument): vscode.CodeAction[] {
        const actions: vscode.CodeAction[] = [];
        const message = diagnostic.message;

        // Handle illegal preprocessor characters
        const preprocessorMatch = message.match(this.ERROR_PATTERNS.illegalPreprocessor);
        if (preprocessorMatch && preprocessorMatch[1]) {
            const illegalChar = preprocessorMatch[1];
            
            const fix = new vscode.CodeAction(`Remove illegal character '${illegalChar}'`, vscode.CodeActionKind.QuickFix);
            fix.edit = new vscode.WorkspaceEdit();
            fix.edit.replace(document.uri, diagnostic.range, '');
            fix.diagnostics = [diagnostic];
            actions.push(fix);
            
            // If it's a backslash, suggest using forward slash
            if (illegalChar === '\\') {
                const slashFix = new vscode.CodeAction(`Change '\\' to '/' for path`, vscode.CodeActionKind.QuickFix);
                slashFix.edit = new vscode.WorkspaceEdit();
                slashFix.edit.replace(document.uri, diagnostic.range, '/');
                slashFix.diagnostics = [diagnostic];
                actions.push(slashFix);
            }
        }

        return actions;
    }
} 