import * as vscode from 'vscode';
import { SpriteFileData } from './spriteDataHandler';
import { PaletteColor } from './paletteUtils';

// Possible sprite/tile modes
type SpriteMode = 'sprite8' | 'sprite4' | 'tile8x8' | 'font8x8';

// Define structure for our internal document state
export interface BlockDocumentState {
    uri: vscode.Uri;
    isMapFile: boolean;
    blockDataBytes: Uint8Array; 
    spriteFilePath: string; 
    spriteDataBytes: Uint8Array | null;
    isDirty: boolean;
    mapIndices?: number[];
    mapWidth?: number;
    mapHeight?: number;
    spriteData: SpriteFileData | null;
    spriteFileName: string; 
    spriteMode?: SpriteMode;
    defaultPalette: PaletteColor[];
    customPalette: PaletteColor[] | null;
    customPaletteName: string;
}

// Wrapper class implementing CustomDocument
export class BlockDocument implements vscode.CustomDocument {
    private _state: BlockDocumentState;

    constructor(state: BlockDocumentState) {
        this._state = state;
    }

    public get uri(): vscode.Uri { return this._state.uri; }
    public get state(): BlockDocumentState { return this._state; }

    // Return the raw byte data for saving/backup
    public get documentData(): Uint8Array {
        // For maps, reconstruct the byte array from indices if necessary
        if (this._state.isMapFile && this._state.mapIndices) {
            const expectedSize = (this._state.mapWidth ?? 0) * (this._state.mapHeight ?? 0);
            const mapData = new Uint8Array(expectedSize);
            const indicesLength = this._state.mapIndices.length;
            for (let i = 0; i < expectedSize; i++) {
                mapData[i] = (i < indicesLength) ? (this._state.mapIndices[i] ?? 0) : 0; 
            }
            return mapData;
        }
        // For blocks or if map indices are missing, return the stored bytes
        return this._state.blockDataBytes;
    }

    dispose(): void {
        console.log("Disposing document:", this.uri.toString());
        // Provider should handle removing from its map if necessary
    }
} 